import React from 'react';
import {Collapse,Navbar,NavbarToggler,NavbarBrand,Nav,NavItem,NavLink,UncontrolledDropdown,DropdownToggle,DropdownMenu,DropdownItem } from 'reactstrap';
import './styles/LoginNavbarUser.css';
import  LoginNavbar from '../components/LoginNavbar';
import Pageone from '../pages/Pageone'
import PageEdit from '../pages/PageEdit'
import ReactDOM from 'react-dom'; 
class LoginNavbarUser extends React.Component {
  constructor(props){
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = {
        isOpen: false
    };
  }
  toggle() {
    this.setState({isOpen: !this.state.isOpen});
  }
  handleClick(){
    const container = document.getElementById('root');
    ReactDOM.render(<Pageone/>, container);
    ReactDOM.render(<PageEdit/>, container); 
  };
  render(){
  return (
    <div className="container-fluid">
      <div className="Barra">
        <UncontrolledDropdown className="ul2 nav justify-content-end" nav inNavbar>
          <DropdownToggle nav caret>
            <a href="ponerlink">{sessionStorage.getItem('fullname')} <img className="imageuser" src={sessionStorage.getItem('userpictureurl')} alt="user"/>
            </a>
          </DropdownToggle>
          <DropdownMenu right>
            <DropdownItem href={"/Pageone/userid:"+sessionStorage.getItem('userid')}>
              Inicio
            </DropdownItem>
            <DropdownItem  href={"/PageEdit/userid:"+sessionStorage.getItem('userid')}>
              Edicion
            </DropdownItem>
            <DropdownItem divider />
            <DropdownItem href={"http://localhost:3000"}>
              <strong>Salir</strong>
            </DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown>
      </div>
      <div className="row">
          <LoginNavbar/>
      </div>
      <div className="row Inicio">
      <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.7.0/css/all.css" integrity="sha384-lZN37f5QGtY3VHgisS14W3ExzMWZxybE1SJSEsQp9S+oqd12jhcu+A56Ebc1zFSJ" crossorigin="anonymous"/>
        <Navbar light expand="md">
          <NavbarBrand href="/"/>
          <NavbarToggler onClick={this.toggle}/>
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="mr-auto" navbar>
              <NavItem> 
                <NavLink><a className="nav-link" href={"/Pageone/userid:"+sessionStorage.getItem('userid')} onClick={() => this.handleClick()}><i class="fas fa-home fa-lg"></i> Inicio <span className="sr-only"></span></a></NavLink>
              </NavItem>
              <NavItem>
                <NavLink><a className="nav-link" onClick={() => this.handleClick()} href={"/PageEdit/userid:"+sessionStorage.getItem('userid')}><i class="fas fa-pencil-alt fa-lg"></i> Edicion <span className="sr-only"></span></a></NavLink>
              </NavItem>
              <NavItem>
                <NavLink><a className="nav-link" onClick={() => this.handleClick()} href={"/PageReport/userid:"+sessionStorage.getItem('userid')}><i class="fas fa-file-alt fa-lg"></i> Reporte <span className="sr-only"></span></a></NavLink>
              </NavItem>
              <NavItem>
                <NavLink><a className="nav-link" onClick={() => this.handleClick()} href={"http://localhost:3000"}><i class="fas fa-sign-out-alt fa-lg"></i> Salir <span className="sr-only"></span></a></NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    </div>
  );
} }
export default LoginNavbarUser;