import React from 'react';
import Pagesone from '../components/Pagesone';
import LoginNavbarUser from '../components/LoginNavbarUser';
class Pageone extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount(){
    this.cargarUser();
  }
  cargarUser = async() => {
    try{      
      const response = await fetch(`${sessionStorage.getItem('url')}/webservice/rest/server.php?wstoken=${sessionStorage.getItem('token')}&wsfunction=core_webservice_get_site_info&moodlewsrestformat=json`);    
      const data = await response.json();
      this.setState({
        username: data.username,
        fullname: data.fullname,
        userid: data.userid,
        userpictureurl: data.userpictureurl,
      });     
    }catch (error) {
    }
  };
  render(){
    return(  
      <div> 
       <LoginNavbarUser />
       <Pagesone/>
      </div>
    );
  }
}
export default Pageone;