import React from "react";
import "./styles/Pagefive.css";
import api from "../api";
import { DataTable } from "primereact/datatable";
import { Button } from "primereact/button";
import { Column } from "primereact/column";
import { Dropdown } from "primereact/dropdown";
import { Growl } from "primereact/growl";
import "primereact/resources/themes/nova-light/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
import {Dialog} from 'primereact/dialog';
class PagesFive extends React.Component {
  constructor() {
    super();
    this.state = {
      dtObjetivoResultado: [],
      dtObjetivoCurso: [],
      dtResultado: [],
      modalIsOpen: false,
      objRes2: null
    };
    this.dtUpdateObjRes = {};
    this.Cod_ObjetivoEditor = this.Cod_ObjetivoEditor.bind(this);
    this.Cod_ResultadoEditor = this.Cod_ResultadoEditor.bind(this);
    this.requiredValidator = this.requiredValidator.bind(this);
    this.onRowEditorValidator = this.onRowEditorValidator.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditInit = this.onRowEditInit.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditCancel = this.onRowEditCancel.bind(this);
  }
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  createObjetivoResultado = async e => {
    const cObjetivoResultado = {
      Cod_ResulCursObjetivo: sessionStorage.getItem('cursoid')+'-'+this.state.Cod_Resultado,
      Cod_Resultado: this.state.Cod_Resultado,
      Cod_Curso: sessionStorage.getItem('cursoid')
    };
    await api.objetivoResultadoCrud.create(cObjetivoResultado);
    this.refreshPage();
  };
  updateObjetivoResultado = async (
    pCod_ResulCursObjetivo,
    pCod_Resultado,
    pCod_Curso
  ) => {
    const cObjetivoResultado = {
      Cod_ResulCursObjetivo: pCod_ResulCursObjetivo,
      Cod_Resultado: pCod_Resultado,
      Cod_Curso: pCod_Curso 
    };
    await api.objetivoResultadoCrud.create(cObjetivoResultado);
  };
  deleteObjetivoResultado = async (dCod_ResulCursObjetivo) => {
    const pCod_ResulCursObjetivo = {
      Cod_ResulCursObjetivo: dCod_ResulCursObjetivo
    };
    await api.objetivoResultadoCrud.remove(pCod_ResulCursObjetivo);
    this.refreshPage();
  };
  componentDidMount() {
    const cObjetivoResultado = {
      Cod_Curso :sessionStorage.getItem('cursoid')
    };
    this.traerObjetivosResultado(cObjetivoResultado);
    this.traerObjetivoCurso(cObjetivoResultado);
    this.traerResultado();
  }
  traerObjetivosResultado = async (cObjetivoResultado) => {
    try {   
      const dtObjetivoResultado = await api.objetivoResultadoCrud.listxCurso(cObjetivoResultado);
      this.setState({
        dtObjetivoResultado: dtObjetivoResultado.ObjetivoResultados
      });
    } catch (error) {}
  };
  traerObjetivoCurso = async (cObjetivoResultado) => {
    try {
      const dtObjetivoCurso = await api.cursoObjetivoCrud.listobxCurso(cObjetivoResultado);      
      this.setState({
        dtObjetivoCurso: dtObjetivoCurso.CursoObjetivos
      });
    } catch (error) {}
  };
  traerResultado = async () => {
    try {
      const dtResultado = await api.resultadoEstudianteCrud.listCb();      
      this.setState({
        dtResultado: dtResultado.Resultado
      });
    } catch (error) {}
  };
  displaySelection(data) {
    if (!data || data.length === 0) {
      return <div></div>;
    } else {
      if (data instanceof Array)
        return (
          <ul style={{ textAlign: "left", margin: 0 }}>
            {data.map((slObjetivoResultado, i) => (
              <button key={slObjetivoResultado.Cod_ResulCursObjetivo}>
                {slObjetivoResultado.Cod_ResulCursObjetivo +
                  " - " + slObjetivoResultado.Cod_ResulCursObjetivo}
              </button>
            ))}
          </ul>
        );
      else
        return (
          <button
            onClick={e => this.deleteObjetivoResultado(data.Cod_ResulCursObjetivo)}
            style={{ textAlign: "left" }}
          >
            Eliminar: {data.Cod_ResulCursObjetivo}
          </button>
        );
    }
  }
  onRowEditorValidator(rowData) {
    let value = rowData["Cod_ResulCursObjetivo"];
    return value.length > 0;
  }
  requiredValidator(props) {
    let value = props.rowData[props.field];
    return value && value.length > 0;
  }
  onEditorValueChange(props, value) {
    let updatedObjRes = [...props.value];
    updatedObjRes[props.rowIndex][props.field] = value;
    this.setState({ objRes2: updatedObjRes });
  }
  Cod_ObjetivoEditor(props) {
    return (
      <Dropdown
        value={props.value[props.rowIndex].Cod_Objetivo}
        options={this.state.dtObjetivoCurso}
        onChange={e => this.onEditorValueChange(props, e.value)}
        style={{ width: "100%" }}
        placeholder="Selecciona OB C"
      />
    );
  }
  Cod_ResultadoEditor(props) {
    return (
      <Dropdown
        value={props.value[props.rowIndex].Cod_Resultado}
        options={this.state.dtResultado}
        onChange={e => this.onEditorValueChange(props, e.value)}
        style={{ width: "100%" }}
        placeholder="Selecciona RE"
      />
    );
  }
  onRowEditInit(event) {
    this.dtUpdateObjRes[event.data.Cod_ResulCursObjetivo] = { ...event.data };
    this.objRes2 = { ...event.data };
  }
  onRowEditSave(event) {
    if (this.onRowEditorValidator(event.data)) {
      console.log(event.data);
      this.updateObjetivoResultado(
        event.data.Cod_ResulCursObjetivo, event.data.Cod_Objetivo,
        event.data.Cod_Resultado, event.data.Cod_Curso
      );
      this.growl.show({
        severity: "Actualizado",
        summary: "Exitoso!!",
        detail: "OE is updated"
      });
    } else {
      this.growl.show({
        severity: "error",
        summary: "Error",
        detail: "OE is required"
      });
    }
  }
  onRowEditCancel(event) {
    let objetivoResultado = [...this.state.dtObjetivoResultado];
    objetivoResultado[event.index] = this.dtUpdateObjRes[
      event.data.Cod_ResulCursObjetivo
    ];
    delete this.dtUpdateObjRes[event.data.Cod_ResulCursObjetivo];
    this.setState({
      dtObjetivoResultado: objetivoResultado
    });
  }
  onEditorValueChangeForRowEditing = async (props, value) => {
    let updatedObjRes = [...props.value];
    updatedObjRes[props.rowIndex][props.field] = value;
    this.setState({ objRes2: updatedObjRes });
  };
  refreshPage() {
    window.location.reload(true);
  }
  footer = (
    <div>
      <Button label="Crear" icon="pi pi-check" onBlur={this.onHide} onClick={this.createObjetivoResultado} />
      {/* <Button label="Cancelar" className="p-button-danger" icon="pi pi-times" onClick={this.onHide}/> */}
    </div>
  );
  render() {
    return (
      <div className="container">
        <div>
          <div className="centrar">
            <h2>{sessionStorage.getItem("fullnameCu")}</h2>
          </div>
        </div>
        <div >
          <div className="row centro2 abajo">
            <h2>
              Lista de Resultados del Estudiante
            </h2>
            <div className="col espacioss">
              <div className="col espacioss">
                <Dialog
                  header="Insercin de Resultados del Estudiante"
                  visible={this.state.visible}
                  footer={this.footer}
                  style={{ width: "50vw" }}
                  modal={true}
                  onHide={() => this.setState({ visible: false })}
                >
                  {/* <div className="p-inputgroup">
                    <span className="p-inputgroup-addon" width="80%">
                      OB. Educacional
                    </span>
                    <Dropdown
                      name="Cod_Objetivo"
                      value={this.state.Cod_Objetivo}
                      options={this.state.dtObjetivoCurso}
                      onChange={this.handleChange}
                      style={{ width: "60%" }}
                      placeholder="Selecciona OB"
                    />
                  </div> */}
                  <p/>
                  <div className="p-inputgroup">
                    <span className="p-inputgroup-addon" width="80%">
                      RE. Estudiante
                    </span>
                    <Dropdown
                      name="Cod_Resultado"
                      value={this.state.Cod_Resultado}
                      options={this.state.dtResultado}
                      onChange={this.handleChange}
                      style={{ width: "60%" }}
                      placeholder="Selecciona RE"
                    />
                  </div>
                </Dialog>
                <Button
                  label="Nuevo"
                  onClick={e => this.setState({ visible: true })}
                />
              </div>
            </div>
          </div>
          <div className="row">
            <Growl ref={el => (this.growl = el)} />
            <DataTable
              value={this.state.dtObjetivoResultado}
              selectionMode="single"
              metaKeySelection={false}
              selection={this.state.selecObjetivoResultado}
              header={this.displaySelection(this.state.selecObjetivoResultado)}
              onSelectionChange={e =>
                this.setState({ selecObjetivoResultado: e.value })
              }
              responsive={true}
              editMode="row"
              rowEditorValidator={this.onRowEditorValidator}
              onRowEditInit={this.onRowEditInit}
              onRowEditSave={this.onRowEditSave}
              onRowEditCancel={this.onRowEditCancel}
            >
              {/* <Column
                field="Cod_ResulCursObjetivo"
                header="Codigo"
                style={{ textAlign: "center", width: "20%" }}
              /> */}
              {/* <Column
                field="Cod_Objetivo"
                header="Objetivo"
                style={{ textAlign: "center", width: "20%" }}
                editor={this.Cod_ObjetivoEditor}
              /> */}
              <Column
                field="Cod_Resultado"
                header="Resultado"
                style={{ textAlign: "center", width: "20%" }}
                editor={this.Cod_ResultadoEditor}
              />
              {/* <Column
                field="Cod_Curso"
                header="Curso"
                style={{ textAlign: "left", width: "20%" }}
              /> */}
              <Column
                rowEditor={true}
                style={{ width: "7%", textAlign: "center" }}
              ></Column>
            </DataTable>
          </div>
        </div>
      </div>
    );
  }
}
export default PagesFive;