import React from 'react';
import './styles/PagLogin.css';
import logo from '../images/LogoUAC.png';
/** Este componente permite listar los cursos que el usuario tiene en el presente semestre.
   * @author: Luis Huallparimachi Saire y Joel Sumerente Ayerbe
   * @param wstoken: El parametro wstoken devuelve el token que solicitamos.
   * @param moodlewsrestformat: El parametro moodlewsrestformat permite darnos el formato en el que se mostrara                                  el contenido de la informacion. 
   * @param wsfunction: El parametro wsfunction permite llamar al metodo del servicio de moodle.
   *                    La funcion (core_enrol_get_users_courses) permite obtener una lista de identificadores de curso en los                      que esta inscrito un usuario.           
   * @param userid: El parametro userid devuelve el id del usuario que solicitamos. 
   * @version: 1.0 17/12/2019
  */
class Pagesone extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      userid :{},
      result:[   
      ],
    }
  }
    componentDidMount(){    
    this.traerUsuario();
    }
  traerUsuario = async() => {
    try{      
      var URL = (sessionStorage.getItem('url'))+'/webservice/rest/server.php?wstoken='+(sessionStorage.getItem('token'))+
      '&moodlewsrestformat=json&wsfunction=core_enrol_get_users_courses&userid='+(sessionStorage.getItem('userid'))
      const response = await fetch(URL);    
      const data = await response.json();
      this.setState({
        result : data
      }); 
      }
    catch (error) {
    }    
  };
  createCursos = async(pCursosID, pfullnameC 
    ) => {
    sessionStorage.setItem('cursoid', (pCursosID));
    sessionStorage.setItem('fullnameCu', (pfullnameC));
  };
  render() {    
    return (           
      <div className="container">        
        <div className="row">
          <div className="centro">
          <h1>BIENVENIDOS AL SISTEMA DE INFORMACION DE LA EPIS</h1>
          </div>
        </div>
        <div className="row abajo">
        {this.state.result.map(cursos => (
          cursos.visible === 1 ?
          <div className="col-md-4">
            <div className="card General" id={cursos.id}>
              <div className="col d-none d-lg-block ">
                <img className="card-img-top styleimagen" src={logo} alt="Imagen" />
              </div>
              <div className="card-body">
                <p className="center card-title color">{cursos.fullname}</p>
                <a className="fuente btn-lg btn-block Boton" href={"/Pagetwo/cursoid:"+(cursos.id)}
                onClick={() => this.createCursos(cursos.id, cursos.fullname)} role="button">CURSO</a>
              </div>
             </div>
          </div> :
          <div></div>  
        ))}
        </div>
      </div>  
    );
  }
}
export default Pagesone;