import React from 'react';
import logo from '../images/LogoUAC.png';
import './styles/Pageone.css';

// import './styles/Paglogin.css';
import ModalRepor from "./ModalRepor" ;
/** Este componente lista el contenido del curso, como son las actividades traidas por Unidades
   * @author: Luis Huallparimachi Saire y Joel Sumerente Ayerbe
   * @param wstoken: El parametro wstoken devuelve el token que solicitamos.
   * @param moodlewsrestformat: El parametro moodlewsrestformat permite darnos el formato en el que se mostrara    el contenido de la informacion. 
   * @param wsfunction: El parametro wsfunction permite llamar al metodo del servicio de moodle.
   * La funcion (core_course_get_contents) permite obtener el contenido del curso (modulo+ URL                        de archivo de servicio web).       
   * @param cursoid: El parametro cursoid devuelve el id del curso que solicitamos. 
   * @version: 1.0 03/01/2020
  */
class Pagesthree extends React.Component { 
  state = {
    data : [     
    ],
  }
  componentDidMount(){
    this.traerUnidades();
  }
  traerUnidades = async() => {
    try{      
      var URL = (sessionStorage.getItem('url'))+'/webservice/rest/server.php?wstoken='+(sessionStorage.getItem('token'))+
      '&moodlewsrestformat=json&wsfunction=core_course_get_contents&courseid='+(sessionStorage.getItem('cursoid'))
      const response = await fetch(URL);    
      const data = await response.json();
      this.setState({
        data : data
      }); 
      }
    catch (error) {
    }    
  };
  render() {
    return (  
      <div className="container">
        <div className="row">
          <div className="centro">
          <h1>{sessionStorage.getItem('fullnameCu')}</h1>
          </div>
        </div>
        <div className="row abajo">
          {this.state.data.map(unidad =>(
            unidad.name !== 'Generalidades' && unidad.name !== 'Recursos Adicionales' ? (
            <div className="col-lg-4" id={unidad.id}>
            <a type="button" className="btn fuente" href={"/Pagefour/unidadid:"+(unidad.id)} 
              onClick={() => sessionStorage.setItem('unidadId', (unidad.id))}>
              <div className="card General">
                <div className="card text-black center">
                  <h2>{unidad.name}</h2>
                  <img src={logo} className="card-img styleimagen" href={"/Pagefour/unidadid:"+(unidad.id)} alt="logo"/>
                  <div className="card-img-overlay"></div>
                </div>
              </div>
            </a>
          </div>
          ):<div></div> ))}   
      </div>
    </div>  
    )
  } 
}
export default Pagesthree;