import React from 'react';
import './styles/LoginNavbar.css';
import Logo from '../images/logoUAC_2.png';
class LoginNavbar extends React.Component {
  render() {
    return (
      <div className="container-fluid">
        <div className="row Box_Model">
          <div className="col d-none d-lg-block">
            <div className="col">
              <img src={Logo} alt="logo" className="image"/>
            </div>
            <div className="col letrasL">
              <h4>Universidad <br/>Andina del Cusco</h4>
              <h5>"Acreditada Internacionalmente"</h5>
            </div>
          </div>
          <div className="col">
            <div className="letrasR">
              <h2> Sistema de Informacion</h2>
              <h3>Resultados del estudiante EPIS</h3>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default LoginNavbar;