import React from 'react';
import PagesReport from '../components/PagesReport';
import LoginNavbarUser from '../components/LoginNavbarUser';
class PageReport extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data :[],
        };
      }
  componentDidMount(){
    this.cargarUser();
  }
  cargarUser = async() => {
    try{      
      const response = await fetch(`${sessionStorage.getItem('url')}/webservice/rest/server.php?wstoken=${sessionStorage.getItem('token')}&wsfunction=core_webservice_get_site_info&moodlewsrestformat=json`);    
      const data = await response.json();
      this.setState({
        username: data.username,
        fullname: data.fullname,
        userid: data.userid,
        userpictureurl: data.userpictureurl,
        data: data,
      });     
    }catch (error) {
    }
  };
  render(){
    return(  
      <div> 
       <LoginNavbarUser/>
       <PagesReport fullname={this.state.fullname}/>
      </div>
    );
  }
}
export default PageReport;