import React from "react";
import api from "../api";
import { Button } from "primereact/button";
import "primereact/resources/themes/nova-light/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
import { Dialog } from "primereact/dialog";
import { Page, Text, View, Document, StyleSheet, PDFViewer,} from "@react-pdf/renderer";
import { Table, TableHeader, TableCell, DataTableCell, TableBody,} from "@david.kucsai/react-pdf-table";
import { PDFDocument, PDFText, PDFTable, PDFTableRow, PDFTableColumn, PDFColumns, PDFColumn,} from "react-pdfmake";
const styles = StyleSheet.create({
  body: {
    paddingTop: 35,
    paddingBottom: 65,
    paddingHorizontal: 35,
  },
  title: {
    fontSize: 24,
    textAlign: "center",
  },
  author: {
    fontSize: 12,
    marginBottom: 40,
  },
  subtitle: {
    fontSize: 12,
    margin: 12,
  },
  text: {
    margin: 12,
    fontSize: 14,
    textAlign: "justify",
  },
  image: {
    marginVertical: 15,
    marginHorizontal: 100,
  },
  header: {
    fontSize: 12,
    marginBottom: 20,
    textAlign: "center",
    color: "grey",
  },
  pageNumber: {
    position: "absolute",
    fontSize: 12,
    bottom: 30,
    left: 0,
    right: 0,
    textAlign: "center",
    color: "grey",
  },
  section: {
    margin: 10,
    padding: 10,
    flexGrow: 1,
  },
});
class ModalRepor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dtReporteActividad: [],
      dtResultadoIndicador: [],
      dtAlumno: [],
      dtTAlumno: [],
      dtActividadFinal: [],
      visible : false,
    };
  }
  componentDidMount() {
    const cResultadoIndicador = {
      Cod_Curso: sessionStorage.getItem("cursoid"),
    };
      // this.traerReporte(sessionStorage.getItem("cursoid"), 2, 1);
    this.traerResultadoIndicador(cResultadoIndicador);
  }

  

  traerResultadoIndicador = async (cResultadoIndicador) => {
    try {
      const dtResultadoIndicador = await api.resultadoIndicadorCrud.listreinxCurso(
        cResultadoIndicador
      );
      this.setState({
        dtResultadoIndicador: dtResultadoIndicador.resultadoIndicadors,
      });
    } catch (error) {}
  };
  traerAlumno = async () => {
    try {
      var URL =
        sessionStorage.getItem("url") +
        "/webservice/rest/server.php?wstoken=" +
        sessionStorage.getItem("token") +
        "&moodlewsrestformat=json&wsfunction=core_enrol_get_enrolled_users&courseid=" +
        sessionStorage.getItem("cursoid");
      const response = await fetch(URL);
      const dtAlumno = await response.json();
      this.setState({
        dtTAlumno: dtAlumno,
      });
    } catch (error) {}
    this.state.dtTAlumno.forEach((element) => {
      if (element.institution == null) {
        const nrow = {
          Cod_Persona: element.id,
          Nombres: element.firstname,
          Apellidos: element.lastname,
          Codigo: element.email.replace("@uandina.edu.pe", ""),
        };
        this.setState({
          dtAlumno: [].concat(this.state.dtAlumno, nrow),
          displayDialog: true,
        });
      }
    });
    this.sacarpromedio();
  };
  traerReporte = async (pCod_Curso, pCod_Unidad, pId_Asignacion) => {
    try {
      const crubrica = {
        Cod_Curso: pCod_Curso, Cod_Unidad: pCod_Unidad, Id_Asignacion: pId_Asignacion,
      };
      const dtReporteActividad = await api.Reportes.ReporteActividad(crubrica);
      this.setState({
        dtReporteActividad: dtReporteActividad.RepoActividad,
      });
      this.traerAlumno();
    } catch (error) {}
  };
  sacarpromedio() {
    var appJson = [];
    for (let index = 0; index < this.state.dtAlumno.length; index++) {
      let element = {
        Codigo: this.state.dtAlumno[index].Codigo,
        Nombres: this.state.dtAlumno[index].Nombres,
        Apellidos: this.state.dtAlumno[index].Apellidos,
        id: this.state.dtAlumno[index].Cod_Persona,
        Notas: [],
      }; 
      appJson.push(element);
      for ( let indexy = 0; indexy < this.state.dtReporteActividad.length; indexy++) {
        if (
          this.state.dtAlumno[index].Cod_Persona ==
          this.state.dtReporteActividad[indexy].Cod_Persona
        ) {
          let pNotas = {
            Asignacion: this.state.dtReporteActividad[indexy].NomAsignacion,
            Des_Criterio: this.state.dtReporteActividad[indexy].DesCriterio,
            Puntaje: this.state.dtReporteActividad[indexy].PuntajeFinal,
            Indicador: this.state.dtReporteActividad[indexy].Cod_Indicador,
            Resultado: this.state.dtReporteActividad[indexy].Cod_Resultado,
          };
          element.Notas.push(pNotas);
        }
      }
    }
    var app2Json = [];
    for (let index = 0; index < appJson.length; index++) {
      let element = {
        Codigo: appJson[index].Codigo,
        Nombres: appJson[index].Nombres,
        Apellidos: appJson[index].Apellidos,
        id: appJson[index].id,
        Indicadores: [],
      };
      app2Json.push(element);
      for (
        let indexy = 0;
        indexy < this.state.dtResultadoIndicador.length;
        indexy++
      ) {
        let Indicadores = {
          Indicador: this.state.dtResultadoIndicador[indexy]
            .Cod_IndicarResultado,
          Nom_Indicador: this.state.dtResultadoIndicador[indexy]
            .Cod_Indicador,
          Detalle: [],
        };
        element.Indicadores.push(Indicadores);
        for (let indexz = 0; indexz < appJson[index].Notas.length; indexz++) {
          var cadena =
            sessionStorage.getItem("cursoid")+
            "-" +
            appJson[index].Notas[indexz].Resultado +
            "-" +
            appJson[index].Notas[indexz].Indicador;
            // console.log(cadena);
          if (
            this.state.dtResultadoIndicador[indexy].Cod_IndicarResultado ==
            cadena
          ) {
            let pNotas = {
              Criterio: appJson[index].Notas[indexz].Des_Criterio,
              Puntaje: appJson[index].Notas[indexz].Puntaje,
            };
            Indicadores.Detalle.push(pNotas);
          }
        }
      }
    }
    var app3Json = [];
    for (let index = 0; index < app2Json.length; index++) {
      let element = {
        Codigo: appJson[index].Codigo,
        Nombres: appJson[index].Nombres,
        Apellidos: appJson[index].Apellidos,
        id: appJson[index].id,
        Indicadores: [],
      };
      app3Json.push(element);
      for (
        let indexy = 0;
        indexy < app2Json[index].Indicadores.length;
        indexy++
      ) {
        var cantidad = app2Json[index].Indicadores[indexy].Detalle.length;
        var suma = 0;
        for (
          let x = 0;
          x < app2Json[index].Indicadores[indexy].Detalle.length;
          x++
        ) {
          suma = suma + app2Json[index].Indicadores[indexy].Detalle[x].Puntaje;
        }
        var Nota = suma / cantidad;
        if (Nota >= 0 && Nota <= 1) {
          // console.log('1');
          Nota = Nota;
        }else{
          // console.log('0');   
          Nota = 0;       
        }
        let Indicadores = {
          Indicador: app2Json[index].Indicadores[indexy].Indicador,
          Nom_Indicador: app2Json[index].Indicadores[indexy].Nom_Indicador,
          Nota: Nota,
          Detalle: [],
        };
        element.Indicadores.push(Indicadores);
        for (
          let x = 0;
          x < app2Json[index].Indicadores[indexy].Detalle.length;
          x++
        ) {
          let pNotas = {
            Criterio: app2Json[index].Indicadores[indexy].Detalle[x].Criterio,
            Puntaje: app2Json[index].Indicadores[indexy].Detalle[x].Puntaje,
          };
          Indicadores.Detalle.push(pNotas);
        }
      }
    }
    // console.log(app3Json);
    this.setState({
      dtActividadFinal: app3Json,
    });
  }
  render() {
    return (
      <div>
        <Dialog
          header="REPORTE INDICADOR DE DESEMPEO"
          visible={this.state.visible}
          footer={this.footer}
          style={{ width: "50vw" }}
          modal={true}
          onHide={() => this.setState({ visible: false })}
        >
          <PDFViewer width="790" height="700">
            <Document>
              <Page style={styles.body}>
                <Text style={styles.header} fixed>
                  ~ Escuela Profesional de Ingeneria de Sistemas ~
                </Text>
                <Text style={styles.title}>Indicador</Text>
                {this.state.dtActividadFinal.map((Persona) => (
                  <View
                    style={{ color: "Black", textAlign: "center", margin: 5 }}
                  >
                    <Text style={styles.subtitle}>{Persona.Codigo + ' ' + Persona.Apellidos + ', ' + Persona.Nombres}</Text>
                      <Table
                        data={Persona.Indicadores}                      
                      >                       
                        <TableHeader textAlign={"center"}>
                          <TableCell>INDICADOR DE DESEMPEO</TableCell>
                          <TableCell>NOTA</TableCell>
                        </TableHeader>
                        <TableBody textAlign={"center"}>
                        <DataTableCell getContent={(r) => r.Nom_Indicador}/>
                        <DataTableCell getContent={(r) => r.Nota}/>    
                        </TableBody>
                      </Table>
                  </View>
                ))}
              </Page>
            </Document>
          </PDFViewer>
        </Dialog>
        <Button
          label={this.props.sms}
          onClick={(e) => this.setState({ visible: true })}
          onc
        />
      </div>
    );
  }
}
export default ModalRepor;