import React from 'react';
import Pageone from '../pages/Pageone'
import ReactDOM from 'react-dom'; 
import api from "../api";
// var url = 'https://campus.uandina.edu.pe'
var url = 'http://192.168.0.120'
/** Esta clase define la validacion de credenciales para poder loggearse en el sistema de informacion
   * @author: Luis Huallparimachi Saire y Joel Sumerente Ayerbe
   * @param service: El parametro service permite llamar las funciones del servicio de moodle que se requiera.
   * @param username: El parametro username devuelve al usuario que ingresamos. 
   * @param password: El parametro password devuelve al password que ingresamos. 
   * @param wstoken: El parametro wstoken devuelve el token que solicitamos.
   * @param wsfunction: La funcion .
   * @param moodlewsrestformat: El parametro moodlewsrestformat permite darnos el formato en el que se mostrara                                 el contenido de la informacion. 
   * @version: 2.0 17/09/2019
  */
class Paglogin extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  handleChange = e => { 
    this.setState({
      [e.target.name]: e.target.value,
    });
  };
  handleClick = async() => {
    try{      
      const response = await fetch(`${url}/login/token.php?service=moodle_mobile_app&username=${this.state.users}&password=${this.state.password}`);    
      const data = await response.json();
      this.setState({
          token: data.token,
          error: data.error,
          errorcode: data.errorcode,
      });      
    }catch (error) {
    }
    if((this.state.errorcode)==='invalidlogin'){
      alert((this.state.error));
    }
    else{      
      try{      
        const response = await fetch(`${url}/webservice/rest/server.php?wstoken=${this.state.token}&wsfunction=core_webservice_get_site_info&moodlewsrestformat=json`);    
        const data2 = await response.json();
        this.setState({
          username: data2.username,
          firstname: data2.firstname,
          lastname: data2.lastname,
          fullname: data2.fullname,
          userid: data2.userid,
          userprivateaccesskey: data2.userprivateaccesskey,
          userpictureurl: data2.userpictureurl
        });     
      }catch (error) {
      }
      if (typeof (Storage) !== "undefined") {        
        sessionStorage.setItem('url', url);
        sessionStorage.setItem('token', (this.state.token));
        sessionStorage.setItem('userid', (this.state.userid))
        console.log('1',(this.state.userid));
        sessionStorage.setItem('fullname', (this.state.fullname))
        sessionStorage.setItem('username', (this.state.username))
        sessionStorage.setItem('userpictureurl', (this.state.userpictureurl))
        const cPersona = {
          Cod_Persona: this.state.userid,
          Nombre: this.state.firstname,
          Apellidos: this.state.lastname,
          NomPersona: this.state.username,
          Correo: this.state.username + '@uandina.edu.pe',
          Grado: 'Teacher'
        };
        await api.personaCrud.create(cPersona);
      }
      else{                     
        sessionStorage.setItem('url', url);
        sessionStorage.setItem('token', (this.state.token));
        sessionStorage.setItem('userid', (this.state.userid))
        sessionStorage.setItem('fullname', (this.state.fullname))
        sessionStorage.setItem('username', (this.state.username))
        sessionStorage.setItem('userpictureurl', (this.state.userpictureurl))
        const cPersona = {
          Cod_Persona: this.state.userid,
          Nombre: this.state.firstname,
          Apellidos: this.state.lastname,
          NomPersona: this.state.username,
          Correo: this.state.username + '@uandina.edu.pe',
          Grado: this.state.userprivateaccesskey
        };
        await api.personaCrud.create(cPersona);
      }
      const container = document.getElementById('root');
      ReactDOM.render(<Pageone/>, container);      
    }
  };
  handleSubmit = e => {
    e.preventDefault();
    console.log('Form was submitted');
    console.log(this.state);
  };
  render() {
    return (  
      <div className="container General">
        <div className="card">
        <div className="row">
          <div className="col-sm-12 col-md-6 col-lg-6">
            <form className="Posicion1" onSubmit={this.handleSubmit}>
              <div className="form-group">
                <input type="text" className="form-control" name="users" value={this.state.users} onChange={this.handleChange} placeholder="Nombre de usuario / Correo Electronica"/>
                <small id="user" className="form-text text-muted"></small>
              </div>
              <div className="form-group">
                <input type="password" className="form-control" name="password" value={this.state.password} onChange={this.handleChange} placeholder="Contrasea"/>
              </div>
              {/* <div className="form-group form-check">
                <input type="checkbox" className="form-check-input" id="exampleCheck1"/>
                <label className="form-check-label" htmlFor="exampleCheck1">Recordar nombre de usuario</label>
              </div> */}
              <div className="form-group">                
                <button onClick={() => this.handleClick()} href="/pageone" className  ="fuente boton btn-lg btn-block" type= "button">ACCEDER</button>
              </div>
            </form>
          </div>
          <div className="col-sm-12 col-md-6 col-lg-6">
              <div className="Posicion2">
                <a className="color" href="https://erp.uandina.edu.pe/pwd/paso1.php">Olvid su nombre de usuario o contrasea?</a>
                <p>Las 'Cookies' deben estar habilitadas en su navegador </p>
                <p>Identifiguese usando su cuenta en:</p>
                <button type="button" className="btn btn-secondary btn-lg btn-block"> Iniciar sesion en google </button>
              </div>
          </div>
        </div>
        </div>
      </div>
    );
  }
}
export default Paglogin;