import React from 'react';
import ListUser from './ListUser';
import './styles/Pagessix.css';
// import './styles/Paglogin.css'; 
class Pagesseven extends React.Component {
    state = {
        data : {
            assignments : [],
        },
        data2 : [],
        data3 : {
          assignments : [],
        },
    }
    componentDidMount(){
        this.traerActividades();
        this.traerAlumnos();        
        this.traerestudiante();
    }
    traerActividades = async() => {
        try{      
          var URL = (sessionStorage.getItem('url'))+'/webservice/rest/server.php?wstoken='+(sessionStorage.getItem('token'))+
          '&moodlewsrestformat=json&wsfunction=mod_assign_get_submissions&assignmentids[0]='+(sessionStorage.getItem('assignid'))
          const response = await fetch(URL);    
          const data = await response.json();
          this.setState({
            data : data
          }); 
          }
        catch (error) {
        }    
    };
    traerAlumnos = async() => {
      try{      
        var URL = (sessionStorage.getItem('url'))+'/webservice/rest/server.php?wstoken='+(sessionStorage.getItem('token'))+
        '&moodlewsrestformat=json&wsfunction=core_enrol_get_enrolled_users&courseid='+(sessionStorage.getItem('cursoid'))
        const response = await fetch(URL);    
        const data2 = await response.json();
        this.setState({
          data2 : data2
        }); 
        }
      catch (error) {
      }    
    };  
  traerestudiante = async() => {
    try{      
      var URL = (sessionStorage.getItem('url'))+'/webservice/rest/server.php?wstoken='+(sessionStorage.getItem('token'))+
      '&moodlewsrestformat=json&wsfunction=mod_assign_get_user_mappings&assignmentids[]='+(sessionStorage.getItem('assignid'))
      const response = await fetch(URL);    
      const data3 = await response.json();
      this.setState({
        data3: {
          assignments : data3.assignments
        }
      }); 
      }
    catch (error) {
    }    
  };
  crearUsuario(){
  }
  salvarNotas(){
  }
  render() {
    return (
      <div className="myClass">
        {this.state.data.assignments.map(lis => (                    
          lis.submissions.map(usuarios => (
            this.state.data3.assignments.map(mapp => mapp.mappings.map(st => 
              usuarios.userid === st.userid ? 
              <div className="row">                    
                <div className="col-12">
                  <a id={usuarios.userid} class="list-group-item list-group-item-action" href="ponerlink">
                  <ListUser userid={usuarios.userid}/></a>
                </div> 
                {this.salvarNotas()}          
              </div>:
              console.log("")
            ))
          ))
        ))}        
        <p>
        </p>
      </div>
    );
  }
}
export default Pagesseven;