import React from "react";
import "./styles/Edicion.css";
import api from "../api";
import { Button, ButtonGroup, Modal, ModalHeader, ModalBody, ModalFooter, InputGroup, InputGroupAddon, Input} from "reactstrap";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { InputText } from "primereact/inputtext";
import { Dropdown } from "primereact/dropdown";
import { Growl } from "primereact/growl";
import "primereact/resources/themes/nova-light/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
class PageEditIN extends React.Component {
  constructor() {
    super();
    this.state = {
      dtIndicadores: [],
      dtIndiNivel: [],
      dtResultados: [],
      modalIsOpen: false,
      ind2: null
    };
    this.dtUpdatInd= {};
    this.IndicadorEditor = this.IndicadorEditor.bind(this);
    this.Cod_IndicadorNivelEditor = this.Cod_IndicadorNivelEditor.bind(this);
    this.DescripcionEditor = this.DescripcionEditor.bind(this);
    this.requiredValidator = this.requiredValidator.bind(this);
    this.editorForRowEditing = this.editorForRowEditing.bind(this);
    this.onRowEditorValidator = this.onRowEditorValidator.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditInit = this.onRowEditInit.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditCancel = this.onRowEditCancel.bind(this);
  };
  ToggleModal() {
    this.setState({
      modalIsOpen: !this.state.modalIsOpen
    });
  }
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  createIndicador = async e => {
    const cIndicador = {
      Cod_Resultado: this.state.Cod_Resultado,
      Cod_Indicadornivel: this.state.Cod_Indicadornivel,
      Cod_Indicador: this.state.Cod_Indicador,
      DescripcionLarga: this.state.DescripcionLarga,
      DescripcionCorta: this.state.DescripcionCorta,
      valor: this.state.valor
    };
    await api.indicadorCrud.create(cIndicador);
    this.refreshPage();
  };
  updateIndicador = async (pCod_Indicadornivel, pDescripcionLarga, pDescripcionCorta, pvalor) => {
    const cIndicador = {
      Cod_Indicadornivel: pCod_Indicadornivel,
      DescripcionLarga: pDescripcionLarga,
      DescripcionCorta: pDescripcionCorta,
      valor: pvalor
    };
    await api.indicadorCrud.create(cIndicador);
  };
  deleteInicador = async (dCod_indicador) => {
    console.log(dCod_indicador);
    const pCod_indicador = {
      Cod_Resultado: dCod_indicador.Cod_Resultado,
      Cod_Indicador: dCod_indicador.Cod_Indicador,
      Cod_Indicadornivel: dCod_indicador.Cod_Indicadornivel
    };
    await api.indicadorCrud.remove(pCod_indicador);
    this.refreshPage();
  };
  traerIndicador = async () => {
    try {
      const dtIndicadores = await api.indicadorCrud.list();
      this.setState({
        dtIndicadores: dtIndicadores.Indicadores
      });
    } catch (error) {}
  };
  traerResultado = async () => {
    try {
      const dtResultados = await api.resultadoEstudianteCrud.listCb();
      this.setState({
        dtResultados: dtResultados.Resultado
      });
    } catch (error) {}
  };
  traerIndicadorNivel = async () => {
    try {
      const dtIndiNivel = await api.IndiNivel.listCb();
      this.setState({
        dtIndiNivel: dtIndiNivel.Indicadores
      });
    } catch (error) {}
  };
  componentDidMount() {
    this.traerIndicador();
    this.traerIndicadorNivel();
    this.traerResultado();
  }
  onRowEditorValidator(rowData) {
    let value = rowData["DescripcionLarga"];
    return value.length > 0;
  }
  Cod_ResultadoEditor(props) {
    return this.inputTextEditor(props, "Cod_Resultado");
  }
  Cod_IndicadorNivelEditor(props) {
    return this.inputTextEditor(props, "Cod_IndicadorNivel");
  }
  DescripcionEditor(props) {
    return this.inputTextEditor(props, "DescripcionLarga");
  }
  requiredValidator(props) {
    let value = props.rowData[props.field];
    return value && value.length > 0;
  }
  onEditorValueChange(props, value) {
    let updatedInd = [...props.value];
    updatedInd[props.rowIndex][props.field] = value;
    this.setState({ ind2: updatedInd });
  }
  IndicadorEditor(props) {
    return (
      <Dropdown 
        value={props.value[props.rowIndex].Cod_Indicadornivel}
        options={this.state.dtIndiNivel}
        onChange={(e) => this.onEditorValueChange(props, e.value)}
        placeholder="Indicador"
        responsive={true}
      />
    );
  }
  onRowEditInit(event) {
    this.dtUpdatInd[event.data.Cod_Indicador] = { ...event.data };
    this.ind2 = { ...event.data };
  }
  onRowEditSave(event) {
    if (this.onRowEditorValidator(event.data)) {
      this.updateIndicador(
        event.data.Cod_Indicadornivel,
        event.data.DescripcionLarga,
        event.data.DescripcionCorta,
        event.data.valor
      );
      this.growl.show({
        severity: "Actualizado",
        summary: "Exitoso!!",
        detail: "IN is updated"
      });
    } else {
      this.growl.show({
        severity: "error",
        summary: "Error",
        detail: "IN is required"
      });
    }
  }
  onRowEditCancel(event) {
    let indicador = [...this.state.dtIndicadores];
    indicador[event.index] = this.dtUpdatInd[event.data.Cod_Indicador];
    delete this.dtUpdatInd[event.data.Cod_Indicador];
    this.setState({
      dtIndicadores: indicador
    });
  }
  onEditorValueChangeForRowEditing = async (props, value) => {
    let updatedInd = [...props.value];
    updatedInd[props.rowIndex][props.field] = value;
    this.setState({ ind2: updatedInd });
  };
  editorForRowEditing(props, field) {
    return (
      <InputText
        type="text"
        value={props.rowData[field]}
        onChange={e =>
          this.onEditorValueChangeForRowEditing(props, e.target.value)
        }
      />
    );
  }
  refreshPage() {
    window.location.reload(true);
  }
  displaySelection(data) {
    if (!data || data.length === 0) {
      return <div></div>;
    } else {
      if (data instanceof Array)
        return (
          <ul style={{ textAlign: "left", margin: 0 }}>
            {data.map((slIndicador, i) => (
              <button key={slIndicador.Cod_Indicador}>
                {slIndicador.Cod_Indicador + " - " + slIndicador.Cod_Indicador}
              </button>
            ))}
          </ul>
        );
      else
        return (
          <button
            onClick={e => this.deleteInicador(data)}
            style={{ textAlign: "left" }}
          >
            Eliminar: {data.Cod_Indicador}
          </button>
        );
    }
  }
  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="centro">
            <h1>Indicadores</h1>
          </div>
        </div>
        <div className="row centro2 abajo">
          <h2>Lista de Indicadores</h2>
          <div className="col espacioss">
            <ButtonGroup>
              <div className="col">
                <Button
                  outline
                  color="primary mr-2"
                  size="lg"
                  onClick={this.ToggleModal.bind(this)}
                >
                  <i class="fas fa-plus-circle"></i>
                </Button>
                <Modal isOpen={this.state.modalIsOpen}>
                  <ModalHeader toggle={this.ToggleModal.bind(this)}>
                    Nuevo Indicador
                  </ModalHeader>
                  <ModalBody>
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Codigo R.E.
                      </InputGroupAddon>
                      <Dropdown
                        name="Cod_Resultado"
                        options={this.state.dtResultados}
                        onChange={this.handleChange}
                        value={this.state.Cod_Resultado}
                        style={{ width: "70%" }}
                        placeholder="Selecciona Resultado"
                      />
                    </InputGroup>
                    <br />
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Codigo Nivel
                      </InputGroupAddon>
                      <Dropdown
                        name="Cod_Indicadornivel"
                        options={this.state.dtIndiNivel}
                        onChange={this.handleChange}
                        value={this.state.Cod_Indicadornivel}
                        style={{ width: "70%" }}
                        placeholder="Selecciona Indicadores"
                      />
                    </InputGroup>
                    <br />
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Codigo Indicador
                      </InputGroupAddon>
                      <Input placeholder="Codigo Indicador"
                        value={this.state.Cod_Indicador}
                        onChange={this.handleChange}
                        type="text"
                        className="form-control"
                        name="Cod_Indicador"/>
                    </InputGroup>
                    <br />
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Descripcion Larga
                      </InputGroupAddon>
                      <Input placeholder="Descripcion Larga"
                        value={this.state.DescripcionLarga}
                        onChange={this.handleChange}
                        type="text"
                        className="form-control"
                        name="DescripcionLarga" />
                    </InputGroup>
                    <br />
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Descripcion Corta
                      </InputGroupAddon>
                      <Input placeholder="Descripcion Corta"
                        value={this.state.DescripcionCorta}
                        onChange={this.handleChange}
                        type="text"
                        className="form-control"
                        name="DescripcionCorta" />
                    </InputGroup>
                    <br />
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Valor
                      </InputGroupAddon>
                      <Input placeholder="Valor del Indicador"
                        value={this.state.Valor}
                        onChange={this.handleChange}
                        type="number"
                        className="form-control"
                        name="valor"/>
                    </InputGroup>
                  </ModalBody>
                  <ModalFooter>
                    <Button
                      color="primary"
                      onBlur={this.createIndicador}
                      onClick={this.ToggleModal.bind(this)}
                    >
                      GUARDAR
                    </Button>
                    <Button
                      color="danger"
                      onClick={this.ToggleModal.bind(this)}
                    >
                      CANCELAR
                    </Button>
                  </ModalFooter>
                </Modal>
              </div>
            </ButtonGroup>
          </div>
        </div>
        <div className="">
        <Growl ref={el => (this.growl = el)} />
          <DataTable
            value={this.state.dtIndicadores}
            selectionMode="single"
            metaKeySelection={false}
            selection={this.state.selecIndicador}
            header={this.displaySelection(this.state.selecIndicador)}
            onSelectionChange={e => this.setState({ selecIndicador: e.value })}
            responsive={true}
            editMode="row"
            rowEditorValidator={this.onRowEditorValidator}
            onRowEditInit={this.onRowEditInit}
            onRowEditSave={this.onRowEditSave}
            onRowEditCancel={this.onRowEditCancel}
          >
            <Column
              field="Cod_Indicador"
              header="Indicador"
              style={{ textAlign: "center", width: "8%" }}
            />
            <Column
              field="Cod_Resultado"
              header="Resultado"
              style={{ textAlign: "center", width: "10%" }}
            />
            <Column
              field="Cod_Indicadornivel"
              header="Nivel"
              style={{ textAlign: "center", width: "10%" }}
              editor={this.IndicadorEditor}
            />
            <Column
              field="DescripcionLarga"
              header="Descripcion Larga"
              style={{ textAlign: "center", width: "30%" }}
              editor={props => this.editorForRowEditing(props, "DescripcionLarga")}
            />
            <Column
              field="DescripcionCorta"
              header="Descripcion Corta"
              style={{ textAlign: "center", width: "30%" }}
              editor={props => this.editorForRowEditing(props, "DescripcionCorta")}
            />
            <Column
              field="valor"
              header="Valor"
              style={{ textAlign: "center", width: "10%" }}
              editor={props => this.editorForRowEditing(props, "Valor")}
            />
            <Column
              rowEditor={true}
              style={{ width: "7%", textAlign: "center" }}
            ></Column>
          </DataTable>
        </div>
      </div>
    );
  }
}
export default PageEditIN;