import React from 'react';
import './styles/Pagessix.css';
// import './styles/Paglogin.css';
import api from "../api";
import { DataTable } from "primereact/datatable";
import { Button } from "primereact/button";
import { Column } from "primereact/column";
import { Dropdown } from "primereact/dropdown";
import { Growl } from "primereact/growl";
import "primereact/resources/themes/nova-light/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
import {Dialog} from 'primereact/dialog';
class Pagessix extends React.Component {
  constructor() {
    super();
    this.state = {
      dtCursoObjetivo: [],
      dtObjetivo: [],
      modalIsOpen: false,
      curObj2: null
    };
    this.dtUpdateCurObj = {};
    this.Cod_ObjetivoEditor = this.Cod_ObjetivoEditor.bind(this);
    this.requiredValidator = this.requiredValidator.bind(this);
    this.onRowEditorValidator = this.onRowEditorValidator.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditInit = this.onRowEditInit.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditCancel = this.onRowEditCancel.bind(this);
  }
  ToggleModal() {
    this.setState({
      modalIsOpen: !this.state.modalIsOpen
    });
  }
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  createCursoObjetivo = async e => {
    // console.log('cCursoObjetivo'+this.state.Cod_Objetivo);
    const cCursoObjetivo = {
      Cod_CursoObjetivo: sessionStorage.getItem('cursoid')+'-'+this.state.Cod_Objetivo,
      Cod_Objetivo: this.state.Cod_Objetivo,
      Cod_Curso: sessionStorage.getItem('cursoid')
    };
    await api.cursoObjetivoCrud.create(cCursoObjetivo);
    this.refreshPage();
  };
  updateCursoObjetivo = async (
    pCod_CursoObjetivo,
    pCod_Objetivo,
    pCod_Curso
  ) => {
    const cCursoObjetivo = {
      Cod_CursoObjetivo: pCod_CursoObjetivo,
      Cod_Objetivo: pCod_Objetivo,
      Cod_Curso: pCod_Curso
    };
    await api.cursoObjetivoCrud.create(cCursoObjetivo);
  };
  deleteCursoObjetivo = async (dCod_CursoObjetivo) => {
    const pCod_CursoObjetivo = {
      Cod_CursoObjetivo: dCod_CursoObjetivo
    };
    await api.cursoObjetivoCrud.remove(pCod_CursoObjetivo);
  };
  componentDidMount() {
    
    this.traerObjetivo();
    const cCursoObjetivo = {
      Cod_Curso :sessionStorage.getItem('cursoid')
    };
    this.traerCursoObjetivos(cCursoObjetivo);
    // console.log(cCursoObjetivo);
    // console.log(this.state.dtCursoObjetivo);
  }
  traerCursoObjetivos = async (cCursoObjetivo) => {
    try {   
      const dtCursoObjetivo = await api.cursoObjetivoCrud.listxCurso(cCursoObjetivo);
      // console.log(dtCursoObjetivo.CursoObjetivos);
      
      this.setState({
        dtCursoObjetivo: dtCursoObjetivo.CursoObjetivos
      });
      console.log(this.state.dtCursoObjetivo);
      
    } catch (error) {}
    
  };
  traerObjetivo = async () => {
    try {
      const dtObjetivo = await api.objetivosEducacionalesCrud.listCb();
      this.setState({
        dtObjetivo: dtObjetivo.Objetivos
      });
      
    } catch (error) {}
  };
  displaySelection(data) {
    if (!data || data.length === 0) {
      return <div></div>;
    } else {
      if (data instanceof Array)
        return (
          <ul style={{ textAlign: "left", margin: 0 }}>
            {data.map((slCursoObjetivo, i) => (
              <button key={slCursoObjetivo.Cod_CursoObjetivo}>
                {slCursoObjetivo.Cod_CursoObjetivo +
                  " - " +
                  slCursoObjetivo.Cod_CursoObjetivo}
              </button>
            ))}
          </ul>
        );
      else
        return (
          <button
            onClick={e => this.deleteCursoObjetivo(data.Cod_CursoObjetivo)}
            style={{ textAlign: "left" }}
          >
            Eliminar: {data.Cod_CursoObjetivo}
          </button>
        );
    }
  }
  onRowEditorValidator(rowData) {
    let value = rowData["Cod_CursoObjetivo"];
    return value.length > 0;
  }
  requiredValidator(props) {
    let value = props.rowData[props.field];
    return value && value.length > 0;
  }
  onEditorValueChange(props, value) {
    let updatedCurObj = [...props.value];
    updatedCurObj[props.rowIndex][props.field] = value;
    this.setState({ curObj2: updatedCurObj });
  }
  Cod_ObjetivoEditor(props) {
    return (
      <Dropdown
        value={props.value[props.rowIndex].Cod_Objetivo}
        options={this.state.dtObjetivo}
        onChange={e => this.onEditorValueChange(props, e.value)}
        style={{ width: "100%" }}
        placeholder="Selecciona OB"
      />
    );
  }
  onRowEditInit(event) {
    this.dtUpdateCurObj[event.data.Cod_CursoObjetivo] = { ...event.data };
    this.curObj2 = { ...event.data };
  }

  onRowEditSave(event) {
    if (this.onRowEditorValidator(event.data)) {
      this.updateCursoObjetivo(
        event.data.Cod_CursoObjetivo,
        event.data.Cod_Objetivo,
        event.data.Cod_Curso
      );
      this.growl.show({
        severity: "Actualizado",
        summary: "Exitoso!!",
        detail: "OE is updated"
      });
    } else {
      this.growl.show({
        severity: "error",
        summary: "Error",
        detail: "OE is required"
      });
    }
  }
  onRowEditCancel(event) {
    let cursoObjetivo = [...this.state.dtCursoObjetivo];
    cursoObjetivo[event.index] = this.dtUpdateCurObj[
      event.data.Cod_CursoObjetivo
    ];
    delete this.dtUpdateCurObj[event.data.Cod_CursoObjetivo];
    this.setState({
      dtCursoObjetivo: cursoObjetivo
    });
  }
  onEditorValueChangeForRowEditing = async (props, value) => {
    let updatedCursoObj = [...props.value];
    updatedCursoObj[props.rowIndex][props.field] = value;
    this.setState({ curObj2: updatedCursoObj });
  };
  refreshPage() {
    window.location.reload(true);
  }
  footer = (
    <div>
      <Button label="Crear" icon="pi pi-check" onBlur={this.onHide} onClick={this.createCursoObjetivo} />
      <Button label="Cancelar" className="p-button-danger" icon="pi pi-times" onClick={this.onHide} />
    </div>
  );
  render() {
    return (
      <div className="container">
        <div className="centrar">
          <h1>{sessionStorage.getItem("fullnameCu")}</h1>
        </div>
        <div className="row centrar abajo">
          <h2>Lista de Objetivos Educacionales del Curso</h2>
          <div className="col espacioss">
            <Dialog
              header="Insercin Relacin Curso y Objetivos Educacionales"
              visible={this.state.visible}
              footer={this.footer}
              style={{ width: "50vw" }}
              modal={true}
              onHide={() => this.setState({ visible: false })}
            >
              <div className="p-inputgroup">
                <span className="p-inputgroup-addon" width= "80%">OB. Educacional</span>
                <Dropdown
                  name="Cod_Objetivo"
                  value={this.state.Cod_Objetivo}
                  options={this.state.dtObjetivo}
                  onChange={this.handleChange}
                  style={{ width: "60%" }}
                  placeholder="Selecciona OB"
                />
              </div>
            </Dialog>
            <Button
              label="Nuevo"
              onClick={e => this.setState({ visible: true })}
            />
          </div>
        </div>
        <div className="row">
          <Growl ref={el => (this.growl = el)} />
          {/* {console.log(this.state.dtCursoObjetivo)} */}
          <DataTable
            value={this.state.dtCursoObjetivo}            
            selectionMode="single"
            selection={this.state.selecCursoObjetivos}
            header={this.displaySelection(this.state.selecCursoObjetivos)}
            onSelectionChange={e =>
              this.setState({ selecCursoObjetivos: e.value })
            }
            responsive={true}
            editMode="row"
            rowEditorValidator={this.onRowEditorValidator}
            onRowEditInit={this.onRowEditInit}
            onRowEditSave={this.onRowEditSave}
            onRowEditCancel={this.onRowEditCancel}
          >
            <Column
              field="Cod_CursoObjetivo"
              header="Codigo"
              style={{ textAlign: "center", width: "20%" }}
            />
            <Column
              field="Cod_Objetivo"
              header="Objetivo"
              style={{ textAlign: "center", width: "20%" }}
              editor={this.Cod_ObjetivoEditor}
            />
            <Column
              field="Cod_Curso"
              header="Curso"
              style={{ textAlign: "center", width: "20%" }}
            />
            <Column
              rowEditor={true}
              style={{ width: "7%", textAlign: "center" }}
            ></Column>
          </DataTable>
        </div>
      </div>
    );
  }
}
export default Pagessix;