import React from 'react';
// import './styles/Paglogin.css';
class PageEdit extends React.Component {
  render() {    
    return (           
      <div className="container">        
        <div className="row">
          <div className="centro">
          <h1>BIENVENIDOS AL SISTEMA DE INFORMACION DE LA EPIS</h1>
          </div>
        </div>
        <div className="row abajo">
          <div className="col-lg-12">
            <h2>EDICION DE CURSOS</h2>            
            <div className="row">            
              <div className="col-lg-4">
                <div className="card General">
                  <div className="card-body">
                    <p className="center card-title color">ESCUELA       PROFESIONAL</p>
                    <a className="fuente btn-lg btn-block" href={"/PageEditES/userid:"+(sessionStorage.getItem('userid'))} role="button">VER</a>
                  </div>
                </div>
              </div>
              <div className="col-lg-4">
                <div className="card General">
                  <div className="card-body">
                    <p className="center card-title color">OBJETIVOS EDUCACIONALES</p>
                    <a className="fuente btn-lg btn-block" href={"/PageEditOB/userid:"+(sessionStorage.getItem('userid'))} role="button">VER</a>
                  </div>
                </div>
              </div>
              <div className="col-lg-4">
                <div className="card General">
                  <div className="card-body">
                    <p className="center card-title color">RESULTADOS DE ESTUDIANTE</p>
                    <a className="fuente btn-lg btn-block" href={"/PageEditRE/userid:"+(sessionStorage.getItem('userid'))} role="button">VER</a>
                  </div>
                </div>
              </div>
            <div className="col-lg-4">
                <div className="card General">
                  <div className="card-body">
                    <p className="center card-title color">INDICADORES DE DESEMPEO</p>
                    <a className="fuente btn-lg btn-block" href={"/PageEditIN/userid:"+(sessionStorage.getItem('userid'))} role="button">VER</a>
                  </div>
                </div>
              </div>
            </div> 
          </div>
        </div>
      </div>  
    );
  }
}
export default PageEdit;