import React from "react";
import PagesEdit from "../components/PagesEdit";
import LoginNavbarUser from "../components/LoginNavbarUser";
import api from "../api";
import { DataTable } from "primereact/datatable";
import { Button } from "primereact/button";
import { Column } from "primereact/column";
import { Dropdown } from "primereact/dropdown";
import { Growl } from "primereact/growl";
import "primereact/resources/themes/nova-light/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
import { Dialog } from "primereact/dialog";
// import { Toast } from 'primereact/toast';

class PageIndicadorCriterio extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dtRubrica: [],
      dtsRubrica: [],
      dtIndicadores: [],
      deleteProductDialog: false,
    };
    this.IndicadorEditor = this.IndicadorEditor.bind(this);
    this.addNew = this.addNew.bind(this);
    this.actionTemplate = this.actionTemplate.bind(this); 
  }
  
  componentDidMount() {
    this.traerRubrica(
      sessionStorage.getItem("cursoid"),
      this.props.Cid,
      this.props.id
    );
    // console.log(sessionStorage.getItem("cursoid"),
    // this.props.Cid,
    // this.props.id);
    this.traerIndidacor(sessionStorage.getItem("cursoid"));
  }
  traerRubrica = async (pCod_Curso, pCod_Asignacion, pId_Asignacion) => {
    try {
      const rubrica = {
        Cod_Curso: pCod_Curso,
        Cod_Asignacion: pCod_Asignacion,
        Id_Asignacion: pId_Asignacion,
      };
      const dtRubrica = await api.asignacionCrud.rubricalist(rubrica);
      this.setState({
        dtRubrica: dtRubrica.RubricaAsignacion,
      });
    } catch (error) {}
  };
  traerIndidacor = async (pCod_Curso) => {
    try {
      const cIndicador = {
        Cod_Curso: pCod_Curso,
      };
      const dtIndicadores = await api.resultadoIndicadorCrud.ListxCurso(
        cIndicador
      );
      this.setState({
        dtIndicadores: dtIndicadores.resultadoIndicadors,
      });
    } catch (error) {}
  };
  onEditorValueChange(props, value, Cod_Resultado) {
    let updatedCars = [...props.value];
    console.log(Cod_Resultado);
    if (Cod_Resultado == null || Cod_Resultado == "") {
      Cod_Resultado = "0";
    }
    updatedCars[props.rowIndex][props.field] = value;
    this.setState({ cars1: updatedCars });
    this.createIndicadorAsignacion(
      sessionStorage.getItem("cursoid"),
      "R" + this.props.id,
      "C" + this.props.id + updatedCars[props.rowIndex].NivelCriterio,
      updatedCars[props.rowIndex].NivelCriterio,
      updatedCars[props.rowIndex][props.field],
      Cod_Resultado
    );
  }
  createIndicadorAsignacion = async (
    pCod_Curso,
    pCod_Rubrica,
    pCod_Criterio,
    pNivelCriterio,
    pCod_Resultado,
    pTCod_Resultado
  ) => {
    const cIndicadorAsignacion = {
      Cod_Curso: pCod_Curso,
      Cod_Rubrica: pCod_Rubrica,
      Cod_Criterio: pCod_Criterio,
      NivelCriterio: pNivelCriterio,
      Cod_Resultado: pCod_Resultado,
      TCod_Resultado: pTCod_Resultado,
    };
    // console.log(cIndicadorAsignacion);
    await api.asignacionIndicadorCrud.create(cIndicadorAsignacion);
  };
  IndicadorEditor(props) {
    return (
      <Dropdown
        name="value"
        value={props.value[props.rowIndex].Cod_Resultado}
        options={this.state.dtIndicadores}
        onChange={(e) =>
          this.onEditorValueChange(
            props,
            e.value,
            props.value[props.rowIndex].Cod_Resultado
          )
        }
        style={{ textAlign: "center", width: "100%" }}
        placeholder="Select a Indicador"
      />
    );
  }
  addNew(rowData) {
    const nrow = [
      {
        Cod_Asignacion: rowData.Cod_Asignacion,
        Id_Asignacion: rowData.Id_Asignacion,
        NomRubrica: rowData.NomRubrica,
        NivelCriterio: rowData.NivelCriterio,
        DesCriterio: rowData.DesCriterio,
        Cod_Resultado: "",
      },
    ];
    this.newCar = true;
    this.setState({
      dtRubrica: [].concat(this.state.dtRubrica, nrow),
      displayDialog: true,
    });
  }
  DeleteNew(rowData) {
    const nrow = [
      {
        Cod_Asignacion: rowData.Cod_Asignacion,
        Id_Asignacion: rowData.Id_Asignacion,
        NomRubrica: rowData.NomRubrica,
        NivelCriterio: rowData.NivelCriterio,
        DesCriterio: rowData.DesCriterio,
        Cod_Resultado: "",
      },
    ];
    let temp2Rubrica = this.state.dtRubrica.filter(val =>  val.Cod_AsignacionIndicador !== rowData.Cod_AsignacionIndicador);
    
    this.setState({
      dtRubrica : temp2Rubrica,
    })
    this.deleteCriterio(rowData.Cod_AsignacionIndicador)
    // this.toast.show({ severity: 'success', summary: 'Successful', detail: 'criterio Deleted', life: 3000 });
  }
  deleteCriterio = async (dCod_Escuela) => {
    const pCod_Escuela = {
      Cod_Asignacion: dCod_Escuela
    };
    console.log(pCod_Escuela);
    await api.asignacionIndicadorCrud.remove(pCod_Escuela);
  };

  deleteSelectedProducts() {
    let dteRubrica = this.state.dtRubrica.filter(val => !this.state.selectedProducts.includes(val));
    this.setState({
        dteRubrica
    });
    this.toast.show({ severity: 'success', summary: 'Successful', detail: 'criterio Deleted', life: 3000 });
  }

  refreshPage() {
    window.location.reload(true);
  }
  actionTemplate(rowData) {
    return (
      <div>
        <Button
          type="button"
          icon="pi pi-plus"
          className="p-button-success"
          style={{ marginRight: ".4em" }}
          onClick={(e) => this.addNew(rowData)}
        />
        <Button
          type="button"
          icon="pi pi-times"
          className="p-button-danger"
          style={{ marginRight: ".4em" }}
          onClick={(e) => this.DeleteNew(rowData)}
        />
      </div>
    );
  }
  footer = (
    <div>
      {/* <Button
        label="Aceptar"
        icon="pi pi-check"
        onClick={this.onHide}
      /> */}
    </div>
  );
  render() {
    return (
      <div>
        {/* <Toast ref={(el) => this.toast = el} /> */}
        <Dialog
          header={this.props.name + this.props.id + " " + this.props.Cid}
          visible={this.state.visible}
          footer={this.footer}
          style={{ width: "50vw" }}
          modal={true}
          onHide={() => this.setState({ visible: false })}
        >
          <Growl ref={(el) => (this.growl = el)} />
          <DataTable
            value={this.state.dtRubrica}
            rowGroupMode="rowspan"
            groupField="DesCriterio"
            responsive={true}
            sortOrder={1}
            sortField="DesCriterio"
            responsive={true}
          >
            <Column
              field="DesCriterio"
              header="Criterio"
              style={{ textAlign: "center", width: "2em" }}
            />
            <Column
              header="Indicador"
              field="Cod_Resultado"
              editor={this.IndicadorEditor}
              style={{ textAlign: "left", width: "3.5em" }}
            />
            <Column
              body={this.actionTemplate}
              style={{ textAlign: "center", width: "4em" }}
            />
          </DataTable>
        </Dialog>
        <Button
          label={this.props.name}
          onClick={(e) => this.setState({ visible: true })}
        />
      </div>
    );
  }
}
export default PageIndicadorCriterio;
