import React from "react";
import "./styles/Edicion.css";
import { Button, ButtonGroup, Modal, ModalHeader, ModalBody, ModalFooter, InputGroup, InputGroupAddon, Input } from "reactstrap";
import api from "../api";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { InputText } from "primereact/inputtext";
import { Growl } from "primereact/growl";
import "primereact/resources/themes/nova-light/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
class PageEditRE extends React.Component {
  constructor() {
    super();
    this.state = {
      dtResultado: [],
      modalIsOpen: false,
      resu2: null
    };
    this.dtUpdateRes = {};
    this.NomResultadoEditor = this.NomResultadoEditor.bind(this);
    this.valorEditor = this.valorEditor.bind(this);
    this.DescripcionEditor = this.DescripcionEditor.bind(this);
    this.requiredValidator = this.requiredValidator.bind(this);
    this.editorForRowEditing = this.editorForRowEditing.bind(this);
    this.onRowEditorValidator = this.onRowEditorValidator.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditInit = this.onRowEditInit.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditCancel = this.onRowEditCancel.bind(this);
  }
  ToggleModal() {
    this.setState({
      modalIsOpen: !this.state.modalIsOpen
    });
  }
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  createResultado = async e => {
    const cResultado = {
      Cod_Resultado: this.state.Cod_Resultado,
      NomResultado: this.state.NomResultado,
      Descripcion: this.state.Descripcion,
      valor : this.state.valor
    };
    console.log(cResultado);
    await api.resultadoEstudianteCrud.create(cResultado);
    this.refreshPage();
  };
  updateResultado = async (pCod_Resultado, pNomResultado, pDescripcion, pvalor) => {
    const cResultado = {
      Cod_Resultado: pCod_Resultado,
      NomResultado: pNomResultado,
      Descripcion: pDescripcion,
      valor: pvalor
    };
    await api.resultadoEstudianteCrud.create(cResultado);
  };
  deleteResultado = async (dCod_Resultado) => {
    const pCod_Resultado = {
      Cod_Resultado: dCod_Resultado
    };
    await api.resultadoEstudianteCrud.remove(pCod_Resultado);
    this.refreshPage();
  };
  traerResultadoEducacional = async () => {
    try {
      const dtResultado = await api.resultadoEstudianteCrud.list();
      this.setState({
        dtResultado: dtResultado.Resultados
      });
    } catch (error) {}
  };
  componentDidMount() {
    this.traerResultadoEducacional();
  }
  onRowEditorValidator(rowData) {
    let value = rowData["NomResultado"];
    return value.length > 0;
  }
  NomResultadoEditor(props) {
    return this.inputTextEditor(props, "NomResultado");
  }
  valorEditor(props) {
    return this.inputTextEditor(props, "valor");
  }
  DescripcionEditor(props) {
    return this.inputTextEditor(props, "Descripcion");
  }
  requiredValidator(props) {
    let value = props.rowData[props.field];
    return value && value.length > 0;
  }
  onEditorValueChange(props, value) {
    let updateRes = [...props.value];
    updateRes[props.rowIndex][props.field] = value;
    this.setState({ resu2: updateRes });
  }
  onRowEditInit(event) {
    this.dtUpdateRes[event.data.Cod_Resultado] = { ...event.data };
    this.resu2 = { ...event.data };
  }
  onRowEditSave(event) {
    if (this.onRowEditorValidator(event.data)) {
      this.updateResultado(
        event.data.Cod_Resultado,
        event.data.NomResultado,
        event.data.Descripcion,
        event.data.valor
      );
      this.growl.show({
        severity: "Actualizado",
        summary: "Exitoso!!",
        detail: "RE is updated"
      });
    } else {
      this.growl.show({
        severity: "error",
        summary: "Error",
        detail: "RE is required"
      });
    }
  }
  onRowEditCancel(event) {
    let resultado = [...this.state.dtResultado];
    resultado[event.index] = this.dtUpdateRes[event.data.Cod_Resultado];
    delete this.dtUpdateRes[event.data.Cod_Resultado];
    this.setState({
      dtResultado: resultado
    });
  }
  onEditorValueChangeForRowEditing = async (props, value) => {
    let updateRes = [...props.value];
    updateRes[props.rowIndex][props.field] = value;
    this.setState({ obj2: updateRes });
  };
  editorForRowEditing(props, field) {
    return (
      <InputText
        type="text"
        value={props.rowData[field]}
        onChange={e =>
          this.onEditorValueChangeForRowEditing(props, e.target.value)
        }
      />
    );
  }
  refreshPage() {
    window.location.reload(true);
  }
  displaySelection(data) {
    if (!data || data.length === 0) {
      return <div></div>;
    } else {
      if (data instanceof Array)
        return (
          <ul style={{ textAlign: "left", margin: 0 }}>
            {data.map((slResultado, i) => (
              <button key={slResultado.Cod_Resultado}>
                {slResultado.Cod_Resultado + " - " + slResultado.Cod_Resultado}
              </button>
            ))}
          </ul>
        );
      else
        return (
          <button
            onClick={e => this.deleteResultado(data.Cod_Resultado)}
            style={{ textAlign: "left" }}
          >
            Eliminar: {data.Cod_Resultado}
          </button>
        );
    }
  }
  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="centro">
            <h1>Resultado del Estudiante</h1>
          </div>
        </div>
        <div className="row centro2 abajo">
          <h2>Lista de Resultados del Estudiante</h2>
          <div className="col espacioss">
            <ButtonGroup>
              <div className="col">
                <Button
                  outline
                  color="primary mr-2"
                  size="lg"
                  onClick={this.ToggleModal.bind(this)}
                >
                  <i class="fas fa-plus-circle"></i>
                </Button>
                <Modal isOpen={this.state.modalIsOpen}>
                  <ModalHeader toggle={this.ToggleModal.bind(this)}>
                    Nuevo Resultado del Estudiante
                  </ModalHeader>
                  <ModalBody>
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Codigo R.E.
                      </InputGroupAddon>
                      <Input
                        placeholder="Codigo Resultado del Estudiante"
                        name="Cod_Resultado"
                        value={this.state.Cod_Resultado}
                        onChange={this.handleChange}
                      />
                    </InputGroup>
                    <br />
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Nombre R.E.
                      </InputGroupAddon>
                      <Input
                        placeholder="Nombre Resultado del Estudiante"
                        name="NomResultado"
                        value={this.state.NomResultado}
                        onChange={this.handleChange}
                      />
                    </InputGroup>
                    <br />
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Descripcion
                      </InputGroupAddon>
                      <Input
                        placeholder="Descripcion del Resultado del Estudiante"
                        name="Descripcion"                        
                        value={this.state.Descripcion}
                        onChange={this.handleChange}
                      />
                    </InputGroup>
                    <br />
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Valor
                      </InputGroupAddon>
                      <Input
                        placeholder="Valor del Resultado del Estudiante"
                        type= "number"
                        name="valor"                        
                        value={this.state.valor}
                        onChange={this.handleChange}
                      />
                    </InputGroup>
                  </ModalBody>
                  <ModalFooter>
                    <Button
                      color="primary"
                      onBlur={this.createResultado}
                      onClick={this.ToggleModal.bind(this)}
                    >
                      GUARDAR
                    </Button>
                    <Button
                      color="danger"
                      onClick={this.ToggleModal.bind(this)}
                    >
                      CANCELAR
                    </Button>
                  </ModalFooter>
                </Modal>
              </div>
            </ButtonGroup>
          </div>
        </div>
        <div className="">
          <Growl ref={el => (this.growl = el)} />
          <DataTable
            value={this.state.dtResultado}
            selectionMode="single"
            metaKeySelection={false}
            selection={this.state.selecResultado}
            header={this.displaySelection(this.state.selecResultado)}
            onSelectionChange={e => this.setState({ selecResultado: e.value })}
            responsive={true}
            editMode="row"
            rowEditorValidator={this.onRowEditorValidator}
            onRowEditInit={this.onRowEditInit}
            onRowEditSave={this.onRowEditSave}
            onRowEditCancel={this.onRowEditCancel}
          >
            <Column
              field="Cod_Resultado"
              header="Codigo"
              style={{ textAlign: "center", width: "10%" }}
            />
            <Column
              field="NomResultado"
              header="Nombre"
              style={{ textAlign: "center", width: "15%" }}
              editor={props => this.editorForRowEditing(props, "NomResultado")}
            />
            <Column
              field="Descripcion"
              header="Descripcion"
              style={{ textAlign: "center", width: "60%" }}
              editor={props => this.editorForRowEditing(props, "Descripcion")}
            />
            <Column
              field="valor"
              header="valor"
              style={{ textAlign: "center", width: "8%" }}
              editor={props => this.editorForRowEditing(props, "valor")}
            />
            <Column
              rowEditor={true}
              style={{ width: "7%", textAlign: "center" }}
            ></Column>
          </DataTable>
        </div>
      </div>
    );
  }
}
export default PageEditRE;