import React from 'react';
import './styles/Pagessix.css';
// import './styles/Paglogin.css';
import api from '../api';
// TRAER NOTAS nesesita del id de estudiante el link del servidor de moodle token de sesion y el id de la asignacion generarda
function traerAlumnoNames (studentUID, link, token, assignid){  
  const  URL = link+'/webservice/rest/server.php?wstoken='+token+
  '&moodlewsrestformat=json&wsfunction=core_user_get_users_by_field&field=id&values[0]='+(studentUID);
  var lista  = document.querySelector('#users');
  var item = document.createElement('div');
  fetch(URL)
    .then(res => res.json())
    .then(data => {
    item.innerHTML = data['0'].fullname;
    item.className = "list-group-item list-group-item-action row"
    item.id = 'nam'+studentUID;
    lista.appendChild(item);
    //funcion que traer la notas si la turieva en caso contrrio se ovia
    //traeralumnonotas idusuario servidormoodle tokenusuario idde asignacion
    traerAlumnoNotas(studentUID, link, token, assignid);
    })
}   
//TRAER NOTAS nesesita del id de estudiante el link del servidor de moodle token de sesion y el id de la asignacion generarda
function traerAlumnoNotas (studentUID, link, token, assignid){  
  const  URL = link+'/webservice/rest/server.php?wstoken='+token+
  '&moodlewsrestformat=json&wsfunction=mod_assign_get_submission_status&userid='+(studentUID)+'&assignid='+(assignid)
  var lista  = document.querySelector('#nam'+studentUID);
  var item = document.createElement('p');
  fetch(URL)
    .then(res => res.json())
    .then(data => {
    if(data.lastattempt.gradingstatus === 'graded'){
      item.innerHTML = data.feedback.gradefordisplay;
      item.id = 'not'+studentUID;
      lista.appendChild(item);
      // obtiene cantidad de criterios de rubrica
      let notaasig = document.querySelector('#not'+studentUID).querySelectorAll('tr.criterion');
      for(let i= 0; i < notaasig.length; i++){
        document.querySelector('td.remark').remove();
        // seleciona los criterios de rubrica
        let criterio = notaasig[i].querySelector('td.description'); 
        // seleccion el puntaje alto de la rubrica
        let notaCrt = notaasig[i].querySelector('td.last > div[class=level-wrapper] > div[class=score] > span[class=scorevalue');
        // selecciona el puntaje obtenido de la rubrica
        let notaObt = notaasig[i].querySelector('td[aria-checked=true] > div[class=level-wrapper] > div[class=score] > span[class=scorevalue');
        // saca el puntaje objetino en el criterio
        let puntaje = 2
        //  notaObt.textContent/notaCrt.textContent;
        //imprcion de prueba
        // console.log(studentUID, criterio.textContent, notaCrt.textContent, notaObt.textContent, puntaje); 
      }      
    }   
  })
}   
class Pageseight extends React.Component {
  state = {
    dtstudent : {
      assignments : [],
    },
    dtstudentCurso : [],   
    //aqui se aadio 
    data: {
      resultado:[],
    }, 
  }
  componentDidMount() {
    this.fetchData();
  }
//listando asignatura.list solo probe esto
  fetchData = async () => {
    this.setState({
      loading: true,
      error: null});
      try{
        const data = await api.asignacionCrud.list();
        this.setState({ loading: false,
        data: {resultado: [].concat(this.state.data.resultado,data.resultado)}
        });
      } catch (error) {
        this.setState({ loading: false, error: error});
      }
  };
  componentWillMount(){    
    this.traerAlumnosCurso();      
  }
  traerAlumnosCurso = async() => {
    try{      
      var URL = (sessionStorage.getItem('url'))+'/webservice/rest/server.php?wstoken='+(sessionStorage.getItem('token'))+
      '&moodlewsrestformat=json&wsfunction=core_enrol_get_enrolled_users&courseid='+(sessionStorage.getItem('cursoid'))
      const response = await fetch(URL);    
      const dtstudentCurso = await response.json();
      this.setState({
        dtstudentCurso : dtstudentCurso
      }); 
      }
    catch (error) {
    }    
  };
  render() {
    return (
      <ul id= "users">
        {this.state.dtstudentCurso.map(user =>(
          user.roles.map(student =>(
            student.roleid === 5 &&
            (traerAlumnoNames(user.id, sessionStorage.getItem('url'), sessionStorage.getItem('token'), sessionStorage.getItem('assignid')))            
          ))          
        ))}
      </ul>      
    );    
  }
}
export default Pageseight;