import React from "react";
import "./styles/Edicion.css";
import api from "../api";
import { Button, ButtonGroup, Modal, ModalHeader, ModalBody, ModalFooter, InputGroup, InputGroupAddon, Input } from "reactstrap";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { InputText } from "primereact/inputtext";
import { Dropdown } from "primereact/dropdown";
import { Growl } from "primereact/growl";
import 'primeicons/primeicons.css';
import 'primereact/resources/themes/nova-light/theme.css';
import 'primereact/resources/primereact.css';

class PageEditOB extends React.Component {
  constructor() {
    super();
    this.state = {
      dtObjEduc: [],
      dtEscuela: [],
      modalIsOpen: false,
      obj2: null
    };
    this.dtUpdateObj = {};
    this.EscuelaEditor = this.EscuelaEditor.bind(this);
    this.Cod_EscuelaEditor = this.Cod_EscuelaEditor.bind(this);
    this.DescripcionEditor = this.DescripcionEditor.bind(this);
    this.requiredValidator = this.requiredValidator.bind(this);
    this.editorForRowEditing = this.editorForRowEditing.bind(this);
    this.onRowEditorValidator = this.onRowEditorValidator.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditInit = this.onRowEditInit.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditCancel = this.onRowEditCancel.bind(this);
  }
  ToggleModal() {
    this.setState({
      modalIsOpen: !this.state.modalIsOpen
    });
  }
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  createObjetivo = async e => {
    const cObjetivo = {
      Cod_Objetivo: this.state.Cod_Objetivo,
      Cod_Escuela: this.state.Cod_Escuela,
      Descripcion: this.state.Descripcion
    };
    await api.objetivosEducacionalesCrud.create(cObjetivo);
    this.refreshPage();
  };
  updateObjetivo = async (pCod_Objetivo, pCod_Escuela, pDescripcion) => {
    const cObjetivo = {
      Cod_Objetivo: pCod_Objetivo,
      Cod_Escuela: pCod_Escuela,
      Descripcion: pDescripcion
    };
    await api.objetivosEducacionalesCrud.create(cObjetivo);
  };
  deleteObjetivo = async dCod_Objetivo => {
    const pCod_Objetivo = {
      Cod_Objetivo: dCod_Objetivo
    };
    await api.objetivosEducacionalesCrud.remove(pCod_Objetivo);
    this.refreshPage();
  };
  componentDidMount() {
    this.traerObjetivosEducacional();
    this.traerEscuela();
  }
  traerObjetivosEducacional = async () => {
    try {
      const dtObjEduc = await api.objetivosEducacionalesCrud.list();
      this.setState({
        dtObjEduc: dtObjEduc.Objetivos
      });
    } catch (error) {}
  };
  traerEscuela = async () => {
    try {
      const dtEscuela = await api.escuelaCrud.listCb();
      this.setState({
        dtEscuela: dtEscuela.Escuelas
      });
    } catch (error) {}
  };
  onRowEditorValidator(rowData) {
    let value = rowData["Descripcion"];
    return value.length > 0;
  }
  Cod_EscuelaEditor(props) {
    return this.inputTextEditor(props, "Cod_Escuela");
  }
  DescripcionEditor(props) {
    return this.inputTextEditor(props, "Descripcion");
  }
  requiredValidator(props) {
    let value = props.rowData[props.field];
    return value && value.length > 0;
  }
  onEditorValueChange(props, value) {
    let updatedObj = [...props.value];
    updatedObj[props.rowIndex][props.field] = value;
    this.setState({ obj2: updatedObj });
  }
  EscuelaEditor(props) {
    return (
      <Dropdown
        value={props.value[props.rowIndex].Cod_Escuela}
        options={this.state.dtEscuela}
        onChange={e => this.onEditorValueChange(props, e.value)}
        style={{width: '1em'}}
        // style={{ width: '100%' }}
        placeholder="Selecciona EP"
      />
    );
  }
  onRowEditInit(event) {
    this.dtUpdateObj[event.data.Cod_Objetivo] = { ...event.data };
    this.obj2 = { ...event.data };
  }
  onRowEditSave(event) {
    if (this.onRowEditorValidator(event.data)) {
      this.updateObjetivo(
        event.data.Cod_CursoObjetivo,
        event.data.Cod_Objetivo,
        event.data.Cod_Curso
      );
      this.growl.show({
        severity: "Actualizado",
        summary: "Exitoso!!",
        detail: "OE is updated"
      });
    } else {
      this.growl.show({
        severity: "error",
        summary: "Error",
        detail: "OE is required"
      });
    }
  }
  onRowEditCancel(event) {
    let objetivo = [...this.state.dtObjEduc];
    objetivo[event.index] = this.dtUpdateObj[event.data.Cod_Objetivo];
    delete this.dtUpdateObj[event.data.Cod_Objetivo];
    this.setState({
      dtObjEduc: objetivo
    });
  }
  onEditorValueChangeForRowEditing = async (props, value) => {
    let updatedObj = [...props.value];
    updatedObj[props.rowIndex][props.field] = value;
    this.setState({ obj2: updatedObj });
  };
  editorForRowEditing(props, field) {
    return (
      <InputText
        type="text"
        value={props.rowData[field]}
        onChange={e =>
          this.onEditorValueChangeForRowEditing(props, e.target.value)
        }
      />
    );
  }
  refreshPage() {
    window.location.reload(true);
  }
  displaySelection(data) {
    if (!data || data.length === 0) {
      return <div></div>;
    } else {
      if (data instanceof Array)
        return (
          <ul style={{ textAlign: "left", margin: 0 }}>
            {data.map((slObjetivo, i) => (
              <button key={slObjetivo.Cod_Objetivo}>
                {slObjetivo.Cod_Objetivo + " - " + slObjetivo.Cod_Objetivo}
              </button>
            ))}
          </ul>
        );
      else
        return (
          <button
            onClick={e => this.deleteObjetivo(data.Cod_Objetivo)}
            style={{ textAlign: "left" }}
          >
            Eliminar: {data.Cod_Objetivo}
          </button>
        );
    }
  }
  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="centro">
            <h1>OBJETIVOS EDUCACIONALES</h1>
          </div>
        </div>
        <div className="row centro2 abajo">
          <h2>Lista de Objetivos Educacionales</h2>
          <div className="col espacioss">
            <ButtonGroup>
              <div className="col">
                <Button
                  outline
                  color="primary mr-2"
                  size="lg"
                  onClick={this.ToggleModal.bind(this)}
                >
                  <i class="fas fa-plus-circle"></i>
                </Button>
                <Modal isOpen={this.state.modalIsOpen}>
                  <ModalHeader toggle={this.ToggleModal.bind(this)}>
                    Nuevo Objetivo Educacional
                  </ModalHeader>
                  <ModalBody>
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Codigo Escuela
                      </InputGroupAddon>
                      <Dropdown
                        name="Cod_Escuela"
                        options={this.state.dtEscuela}
                        onChange={this.handleChange}
                        value={this.state.Cod_Escuela}
                        style={{ width: "70%" }}
                        placeholder="Selecciona Escuela Profesional"
                      />
                    </InputGroup>
                    <br />
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Codigo Objetivo
                      </InputGroupAddon>
                      <Input
                        placeholder="Codigo Objetivo Educacional"
                        value={this.state.Cod_Objetivo}
                        onChange={this.handleChange}
                        type="text"
                        className="form-control"
                        name="Cod_Objetivo"
                      />
                    </InputGroup>
                    <br />
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Descripcion
                      </InputGroupAddon>
                      <Input
                        placeholder="Descripcion del Objetivo Educacional"
                        value={this.state.Descripcion}
                        onChange={this.handleChange}
                        type="text"
                        className="form-control"
                        name="Descripcion"
                      />
                    </InputGroup>
                  </ModalBody>
                  <ModalFooter>
                    <Button
                      color="primary"
                      onBlur={this.createObjetivo}
                      onClick={this.ToggleModal.bind(this)}
                    >
                      GUARDAR
                    </Button>
                    <Button
                      color="danger"
                      onClick={this.ToggleModal.bind(this)}
                    >
                      CANCELAR
                    </Button>
                  </ModalFooter>
                </Modal>
              </div>
            </ButtonGroup>
          </div>
        </div>
        <div className="">
          <Growl ref={el => (this.growl = el)} />
          <DataTable
            value={this.state.dtObjEduc}
            selectionMode="single"
            metaKeySelection={false}
            selection={this.state.selecObjetivos}
            responsive={true}
            header={this.displaySelection(this.state.selecObjetivos)}
            onSelectionChange={e => this.setState({ selecObjetivos: e.value })}
            responsive={true}
            editMode="row"
            rowEditorValidator={this.onRowEditorValidator}
            onRowEditInit={this.onRowEditInit}
            onRowEditSave={this.onRowEditSave}
            onRowEditCancel={this.onRowEditCancel}
          >
            <Column
              field="Cod_Escuela"
              header="Escuela"
              style={{height: '3.5em'}}
              // style={{ textAlign: "center", width: "28%" }}
              editor={this.EscuelaEditor}
            />
            <Column
              field="Cod_Objetivo"
              header="Codigo"
              style={{height: '3.5em'}}
              // style={{ textAlign: "center", width: "10%" }}
            />
            <Column
              field="Descripcion"
              header="Descripcion"
              style={{height: '3.5em'}}
              // style={{ textAlign: "left", width: "65%" }}
              editor={props => this.editorForRowEditing(props, "Descripcion")}
            />
            <Column
              rowEditor={true}
              style={{height: '3.5em'}}
              // style={{ width: "7%", textAlign: "center" }}
            ></Column>
          </DataTable>
        </div>
      </div>
    );
  }
}
export default PageEditOB;