import React from 'react';
import './styles/ButtonActions.css';
import './styles/Grade.css';
class ButtonActions extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dtalumnoN:[],
      dtgradeStudent: {
        gradingsummary : {},
        lastattempt :{},
        feedback : {
          grade :  {
            grader : {},
            grade : {},
          },
          gradefordisplay : {},
          gradeddate :{},
          plugins : {},
        },
      },
    };
  }
  componentWillMount(){
    this.traerCalificacionStudent();
    console.log("willmount  tercera");   
  }
  componentDidMount(){   
    console.log("didmounte tercera");    
  }
  componentWillUpdate(){    
    console.log("willupdate  tercera")
  }
  componentDidUpdate(){
    console.log("didupdate  tercera")
  }
  componentWillUnmount(){
    console.log("unwilmount  tercera");    
  }
  traerCalificacionStudent = async() => {
    try{      
      var URL = (sessionStorage.getItem('url'))+'/webservice/rest/server.php?wstoken='+(sessionStorage.getItem('token'))+
      '&moodlewsrestformat=json&wsfunction=mod_assign_get_submission_status&userid='+(this.props.userid)+'&assignid='+(sessionStorage.getItem('assignid'))
      const response = await fetch(URL);    
      const dtgradeStudent = await response.json();
      this.setState({
        dtgradeStudent: { 
          gradingsummary : dtgradeStudent.gradingsummary,
          lastattempt : {
            graded : dtgradeStudent.lastattempt.graded,
            gradingstatus :dtgradeStudent.lastattempt.gradingstatus,
          },
          feedback : {
              grade :  {
                grader : dtgradeStudent.feedback.grade.grader,
                grade : dtgradeStudent.feedback.grade.grade,
              },
              gradefordisplay : dtgradeStudent.feedback.gradefordisplay,
              gradeddate : dtgradeStudent.feedback.gradeddate,
            }
          },
      }); 
      }
    catch (error) {
    }    
  }; 
  render(){        
    return (      
      <div className= "listaUsuario">
        {this.state.dtalumnoN.map(usuarioName =>(                  
          <div id={usuarioName.id} className= "row">
            <div className= "col-3">
              <img className= "imageuser" src= {usuarioName.profileimageurl} alt= "imageuser"/>
              {usuarioName.fullname}
            </div>
            <div className= "col-9">
              {this.state.dtgradeStudent.lastattempt.gradingstatus === 'graded' &&
                <div>
                  <h3 id= "cca">CON CALIFICACIN</h3> 
                  <div id= {usuarioName.id} class= "rubrica" dangerouslySetInnerHTML= {{ __html: this.state.dtgradeStudent.feedback.gradefordisplay}}/>
                </div>               
              }
            </div>
          </div>
        ))}
      </div>      
    );    
  }
}
export default ButtonActions;