import React from "react";
import "./styles/Pagefive.css";
import api from "../api";
import { DataTable } from "primereact/datatable";
import { Button } from "primereact/button";
import { Column } from "primereact/column";
import { Dropdown } from "primereact/dropdown";
import { Growl } from "primereact/growl";
import "primereact/resources/themes/nova-light/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
import {Dialog} from 'primereact/dialog';
class PagesFive extends React.Component {
  constructor() {
    super();
    this.state = {
      dtResultadoIndicador: [],
      dtResultadoObjetivo: [],
      dtIndicador: [],
      modalIsOpen: false,
      resInd2: null
    };
    this.dtUpdateResInd = {};
    this.Cod_ResultadoEditor = this.Cod_ResultadoEditor.bind(this);
    this.Cod_IndicadorEditor = this.Cod_IndicadorEditor.bind(this);
    this.requiredValidator = this.requiredValidator.bind(this);
    this.onRowEditorValidator = this.onRowEditorValidator.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditInit = this.onRowEditInit.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditCancel = this.onRowEditCancel.bind(this);
  }
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  handleChangeC = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
    const cResultado = {
      Cod_ResulCursObjetivo: e.value
    };
    this.traerIndicador(cResultado);
  };
  createResultadoIndicador = async e => {
    const cResultadoIndicador = {
      Cod_IndicarResultado:
        this.state.Cod_Resultado + "-" + this.state.Cod_Indicador,
      Cod_Resultado: this.state.Cod_Resultado,
      Cod_Indicador: this.state.Cod_Indicador,
      Cod_Curso: sessionStorage.getItem("cursoid")
    };
    await api.resultadoIndicadorCrud.create(cResultadoIndicador);
    this.refreshPage();
  };
  updateResultadoIndicador = async (
    pCod_ResulCursObjetivo, pCod_Resultado,
    pCod_Indicador, pCod_Curso
  ) => {
    const cResultadoIndicador = {
      Cod_IndicarResultado: pCod_ResulCursObjetivo, Cod_Resultado: pCod_Resultado, 
      Cod_Indicador: pCod_Indicador, Cod_Curso: pCod_Curso
    };
    await api.resultadoIndicadorCrud.create(cResultadoIndicador);
  };
  deleteResultadoIndicador = async (Cod_IndicarResultadoC) => {
    const pCod_IndicarResultado = {
      Cod_IndicarResultado: Cod_IndicarResultadoC
    };
    await api.resultadoIndicadorCrud.remove(pCod_IndicarResultado);
    this.refreshPage();
  };
  componentDidMount() {
    const cResultadoIndicador = {
      Cod_Curso: sessionStorage.getItem("cursoid")
    };
    this.traerResultadoIndicador(cResultadoIndicador);
    this.traerResultadoCurso(cResultadoIndicador);
  }
  traerResultadoIndicador = async cResultadoIndicador => {
    try {
      const dtResultadoIndicador = await api.resultadoIndicadorCrud.listreinxCurso(
        cResultadoIndicador
      );
      this.setState({
        dtResultadoIndicador: dtResultadoIndicador.resultadoIndicadors
      });
    } catch (error) {}
  };
  traerResultadoCurso = async cResultadoIndicador => {
    try {
      const dtResultadoObjetivo = await api.objetivoResultadoCrud.listrexCurso(
        cResultadoIndicador
      );
      this.setState({
        dtResultadoObjetivo: dtResultadoObjetivo.ObjetivoResultados
      });
    } catch (error) {}
  };
  traerIndicador = async cResultado => {
    try {
      const dtIndicador = await api.indicadorCrud.ListInxRe(cResultado);
      this.setState({
        dtIndicador: dtIndicador.ResultadoIndicadors
      });
    } catch (error) {}
  };
  displaySelection(data) {
    if (!data || data.length === 0) {
      return <div></div>;
    } else {
      if (data instanceof Array)
        return (
          <ul style={{ textAlign: "left", margin: 0 }}>
            {data.map((slObjetivoResultado, i) => (
              <button key={slObjetivoResultado.Cod_IndicarResultado}>
                {slObjetivoResultado.Cod_IndicarResultado +
                  " - " +
                  slObjetivoResultado.Cod_IndicarResultado}
              </button>
            ))}
          </ul>
        );
      else
        return (
          <button
            onClick={e =>
              this.deleteResultadoIndicador(data.Cod_IndicarResultado)
            }
            style={{ textAlign: "left" }}
          >
            Eliminar: {data.Cod_IndicarResultado}
          </button>
        );
    }
  }
  onRowEditorValidator(rowData) {
    let value = rowData["Cod_IndicarResultado"];
    return value.length > 0;
  }
  requiredValidator(props) {
    let value = props.rowData[props.field];
    return value && value.length > 0;
  }
  onEditorValueChange(props, value) {
    let updatedResInd = [...props.value];
    updatedResInd[props.rowIndex][props.field] = value;
    this.setState({ resInd2: updatedResInd });
  }
  Cod_ResultadoEditor(props) {
    return (
      <Dropdown
        value={props.value[props.rowIndex].Cod_Resultado}
        options={this.state.dtResultadoObjetivo}
        onChange={e => this.onEditorValueChange(props, e.value)}
        style={{ width: "100%" }}
        placeholder="Selecciona RE OB"
      />
    );
  }
  Cod_IndicadorEditor(props) {
    return (
      <Dropdown
        value={props.value[props.rowIndex].Cod_Resultado}
        options={this.state.dtIndicador}
        onChange={e => this.onEditorValueChange(props, e.value)}
        style={{ width: "100%" }}
        placeholder="Selecciona IN"
      />
    );
  }
  onRowEditInit(event) {
    this.dtUpdateResInd[event.data.Cod_IndicarResultado] = { ...event.data };
    this.resInd2 = { ...event.data };
  }
  onRowEditSave(event) {
    if (this.onRowEditorValidator(event.data)) {
      console.log(event.data);
      this.updateResultadoIndicador(
        event.data.Cod_IndicarResultado,
        event.data.Cod_Resultado,
        event.data.Cod_Resultado,
        event.data.Cod_Curso
      );
      this.growl.show({
        severity: "Actualizado",
        summary: "Exitoso!!",
        detail: "OE is updated"
      });
    } else {
      this.growl.show({
        severity: "error",
        summary: "Error",
        detail: "OE is required"
      });
    }
  }
  onRowEditCancel(event) {
    let resultadoIndicador = [...this.state.dtResultadoIndicador];
    resultadoIndicador[event.index] = this.dtUpdateResInd[
      event.data.Cod_IndicarResultado
    ];
    delete this.dtUpdateResInd[event.data.Cod_IndicarResultado];
    this.setState({
      dtResultadoIndicador: resultadoIndicador
    });
  }
  onEditorValueChangeForRowEditing = async (props, value) => {
    let updatedResInd = [...props.value];
    updatedResInd[props.rowIndex][props.field] = value;
    this.setState({ resInd2: updatedResInd });
  };
  refreshPage() {
    window.location.reload(true);
  }
  footer = (
    <div>
      <Button
        label="Crear"
        icon="pi pi-check"
        onBlur={this.onHide}
        onClick={this.createResultadoIndicador}
      />
      {/* <Button label="Cancelar" className="p-button-danger" icon="pi pi-times" onClick={this.onHide} /> */}
    </div>
  );
  render() {
    return (
      <div className="container">
        <div>
          <div className="centrar">
            <h1> Indicadores de Desempeo </h1>
            <h2>{sessionStorage.getItem("fullnameCu")}</h2>
          </div>
        </div>
        <div>
          <div className="row centro2 abajo">
            <h2>
              Lista de Indicadores de Desempeo 
            </h2>
            <div className="col espacioss">
              <div className="col espacioss">
                <Dialog
                  header="Relacion Resultados con Indicadores de Desempeo"
                  visible={this.state.visible}
                  footer={this.footer}
                  style={{ width: "50vw" }}
                  modal={true}
                  onHide={() => this.setState({ visible: false })}
                >
                  <div className="p-inputgroup">
                    <span className="p-inputgroup-addon" width="80%">
                      Resultado de Estudiante
                    </span>
                    <Dropdown
                      name="Cod_Resultado"
                      value={this.state.Cod_Resultado}
                      options={this.state.dtResultadoObjetivo}
                      onChange={this.handleChangeC}
                      style={{ width: "60%" }}
                      placeholder="Selecciona RE"
                    />
                  </div>
                  <p />
                  <div className="p-inputgroup">
                    <span className="p-inputgroup-addon" width="80%">
                      Indicadores de Desempeo
                    </span>
                    <Dropdown
                      name="Cod_Indicador"
                      value={this.state.Cod_Indicador}
                      options={this.state.dtIndicador}
                      onChange={this.handleChange}
                      style={{ width: "60%" }}
                      placeholder="Selecciona IN"
                    />
                  </div>
                </Dialog>
                <Button
                  label="Nuevo"
                  onClick={e => this.setState({ visible: true })}
                />
              </div>
            </div>
          </div>
          <div className="row">
            <Growl ref={el => (this.growl = el)} />
            <DataTable
              value={this.state.dtResultadoIndicador}
              selectionMode="single"
              metaKeySelection={false}
              selection={this.state.selecResultadoIndicador}
              header={this.displaySelection(this.state.selecResultadoIndicador)}
              onSelectionChange={e =>
                this.setState({ selecResultadoIndicador: e.value })
              }
              responsive={true}
              editMode="row"
              rowEditorValidator={this.onRowEditorValidator}
              onRowEditInit={this.onRowEditInit}
              onRowEditSave={this.onRowEditSave}
              onRowEditCancel={this.onRowEditCancel}
            >
              {/* <Column
                field="Cod_IndicarResultado"
                header="Codigo"
                style={{ textAlign: "center", width: "20%" }}
              /> */}
              <Column
                field="Cod_Resultado"
                header="Resultado"
                style={{ textAlign: "center", width: "20%" }}
                editor={this.Cod_ResultadoEditor}
              />
              <Column
                field="Cod_Indicador"
                header="Indicador"
                style={{ textAlign: "center", width: "20%" }}
                editor={this.Cod_IndicadorEditor}
              />
              {/* <Column
                field="Cod_Curso"
                header="Curso"
                style={{ textAlign: "left", width: "20%" }}
              /> */}
              <Column
                rowEditor={true}
                style={{ width: "7%", textAlign: "center" }}
              ></Column>
            </DataTable>
          </div>
        </div>
      </div>
    );
  }
}
export default PagesFive;