import React from 'react';
import logo from '../images/LogoUAC.png';
import './styles/Pageone.css';
// import "./styles/Paglogin.css";
import api from "../api";
/** Este componente nos lista los cursos que coindicen con un campo especifico 
  * @author: Luis Huallparimachi Saire y Joel Sumerente Ayerbe
   * @param wstoken: El parametro wstoken devuelve el token que solicitamos.
   * @param moodlewsrestformat: El parametro moodlewsrestformat permite darnos el formato en el que se mostrara    el contenido de la informacion. 
   * @param wsfunction:La funcion (core_course_get_courses_by_fiel) permite obtener los cursos que coinciden con un campo especifico (id/id's, shortname, idnumber, category).
   * @param cursoid: El parametro cursoid devuelve el id del curso que solicitamos. 
   * @version: 1.0 25/12/2019
  */
class Pagestwo extends React.Component {
  state = {
    data : {
      courses:[
      ],},
    dtAlumno : {}
  };
  componentDidMount(){
    this.traerUsuario();
    this.traerAlumno();
  }
  traerAlumno = async() => {
    try{      
      var URL = (sessionStorage.getItem('url'))+'/webservice/rest/server.php?wstoken='+(sessionStorage.getItem('token'))+
      '&moodlewsrestformat=json&wsfunction=core_enrol_get_enrolled_users&courseid='+(sessionStorage.getItem('cursoid'))
      const response = await fetch(URL);    
      const dtAlumno = await response.json();
      this.setState({
        dtAlumno : dtAlumno,
      });       
    }      
    catch (error) {
      }   
    this.state.dtAlumno.forEach(element => {
      if (element.idnumber == null) {
        this.createUsuario(element.id, element.firstname, element.lastname, element.email.replace('@uandina.edu.pe', ''), element.email, 'Student');
      }    
    });
  };
  createUsuario = async (pCod_Persona, pNombre, pApellidos, pNomPersona, pCorreo, pGrado) => {
    const cPersona = {
      Cod_Persona: pCod_Persona,
      Nombre: pNombre,
      Apellidos: pApellidos,
      NomPersona: pNomPersona,
      Correo: pCorreo,
      Grado: pGrado
    };
    await api.personaCrud.create(cPersona);
  };
  traerUsuario = async() => {
    try{      
      var URL = (sessionStorage.getItem('url'))+'/webservice/rest/server.php?wstoken='+(sessionStorage.getItem('token'))+
      '&moodlewsrestformat=json&wsfunction=core_course_get_courses_by_field&field=id&value='+(sessionStorage.getItem('cursoid'))
      const response = await fetch(URL);    
      const dataC = await response.json();
      this.setState({
        data : dataC,
      }); 
    }      
    catch (error) {
    }   
    const cCurso = {
      Cod_Curso: this.state.data.courses[0].id,
      NomCurso: this.state.data.courses[0].fullname,
      Descripcion: this.state.data.courses[0].categoryname,
      Docente: this.state.data.courses[0].contacts[0].fullname,
      Semestre: this.state.data.courses[0].shortname
    };
    await api.cursoCrud.create(cCurso);
  };
  render() {
    return (
      <div className="container">
        <div className="row"> 
        {this.state.data.courses.map(cursos => (
          <div> 
            <div className="centro">                  
              <h1>{cursos.displayname}</h1>    
            </div>  
            <div className="row">            
              {/* <div className="col-lg-3">
                <div className="card General">
                  <div className="col d-none d-lg-block ">
                   <img className="card-img-top styleimagen" src={logo} alt="Imagen" />
                  </div>
                  <div className="card-body">
                    <p className="center card-title color">Objetivos</p>
                    <p className="center card-title color">Educacionales</p>
                    <a className="fuente btn-lg btn-block" href={"/pagesix/cursoid:"+(cursos.id)} role="button">VER</a>
                  </div>
                </div>
              </div> */}
              <div className="col-lg-4">
                <div className="card General">
                  <div className="col d-none d-lg-block ">
                    <img className="card-img-top styleimagen" src={logo} alt="Imagen" />
                  </div>
                  <div className="card-body">
                    <p className="center card-title color">Resultados</p>
                    <p className="center card-title color">del Estudiante</p>
                    <a className="fuente btn-lg btn-block" href={"/pagefive/cursoid:"+(cursos.id)} role="button">VER</a>
                  </div>
                </div>
              </div>
              <div className="col-lg-4">
                <div className="card General">
                  <div className="col d-none d-lg-block ">
                   <img className="card-img-top styleimagen" src={logo} alt="Imagen" />
                  </div>
                  <div className="card-body">
                    <p className="center card-title color">Indicadores</p>
                    <p className="center card-title color">Desempeo</p>
                    <a className="fuente btn-lg btn-block" href={"/pagenine/cursoid:"+(cursos.id)} role="button">VER</a>
                  </div>
                </div>
              </div>
              <div className="col-lg-4">
                <div className="card General">
                  <div className="col d-none d-lg-block ">
                    <img className="card-img-top styleimagen" src={logo} alt="Imagen" />
                  </div>
                  <div className="card-body">
                    <p className="center card-title color">Actividades</p>
                    <p className="center card-title color">del Curso</p>
                    <a className="fuente btn-lg btn-block" href={"/pagethree/cursoid:"+(cursos.id)} role="button">VER</a>
                  </div>
                </div>
              </div>
            </div> 
          </div>
        ))}
        </div>
        {/* 
         */}
      </div>
    );
  }
}
export default Pagestwo;