import React from "react";
import "./styles/PagLogin.css";
import logo from "../images/LogoUAC.png";
import api from "../api";
import ModalRepor from "./ModalRepor";
import Prueba from "./Prueba";
import { Dialog } from "primereact/dialog";
import { Button } from "primereact/button";

/** Este componente permite listar los cursos que el usuario tiene en el presente semestre.
 * @author: Luis Huallparimachi Saire y Joel Sumerente Ayerbe
 * @param wstoken: El parametro wstoken devuelve el token que solicitamos.
 * @param moodlewsrestformat: El parametro moodlewsrestformat permite darnos el formato en el que se mostrara                                  el contenido de la informacion.
 * @param wsfunction: El parametro wsfunction permite llamar al metodo del servicio de moodle.
 *                    La funcion (core_enrol_get_users_courses) permite obtener una lista de identificadores de curso en los                      que esta inscrito un usuario.
 * @param userid: El parametro userid devuelve el id del usuario que solicitamos.
 * @version: 1.0 17/12/2019
 */

class PagesReport extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      userid: {},
      result: [],
      dtcurso: [],
    };
  }

  componentDidMount() {
    this.traerCurso();
  }

  traerCurso = async () => {
    const docente = {
      Docente: sessionStorage.getItem("fullname"),
    };
    try {
      const dtcurso = await api.cursoCrud.docentecurso(docente);
      // console.log(dtCursoObjetivo.CursoObjetivos);
      this.setState({
        dtcurso: dtcurso.Cursos,
      });
    } catch (error) {}
  };

  //   editorForRowEditing(props, field) {
  //     return (
  //       <InputText
  //         type="text"
  //         value={props.rowData[field]}
  //         onChange={e =>
  //           this.onEditorValueChangeForRowEditing(props, e.target.value)
  //         }
  //       />
  //     );
  //   }

  validate = async (cod_curso) => {
    const curso = {
      Cod_Curso: cod_curso,
    };    
    sessionStorage.setItem('cursoid', (cod_curso));
    try {
      const rpta = await api.Reportes.validate(curso);
      this.setState({
        Mensaje: rpta.RepoActividad[0].MERROR,
      });
      this.state.Mensaje && this.setState({ visible: true });
    } catch (error) {}
  };

  createTipo = async(pTipo) => {
    sessionStorage.setItem('rtipo', (pTipo));
  };

  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="centro">
            <h1>Cursos Procesados </h1>
          </div>
        </div>
        <div className="row abajo">
          {this.state.dtcurso.map((cursos) => (
            <div className="col-md-4">
              <div className="card General" id={cursos.Cod_Curso}>
                <div className="col d-none d-lg-block ">
                  <img
                    className="card-img-top styleimagen"
                    src={logo}
                    alt="Imagen"
                  />
                </div>
                <div className="card-body">
                  <p className="center card-title color">{cursos.NomCurso}</p>
                  <a
                    className="fuente btn-lg btn-block Boton" //href=""
                    href="#"
                    onClick={(e) => this.validate(cursos.Cod_Curso)}
                    role="button"
                  >
                    CURSO
                  </a>
                </div>
              </div>
              <div>
                {this.state.Mensaje == "GOZZUUUUU" ? (
                  //  <ModalRepor sms ={this.state.Mensaje}/>
                  <Dialog
                    header="Selecciona Tu Reporte"
                    visible={this.state.visible}
                    footer={this.footer}
                    style={{ width: "50vw" }}
                    modal={true}
                    onHide={() => this.setState({ visible: false })}
                  > 
                  <div className="row">
                    <div className="col-lg-6">
                      <div className="card General">
                        <div className="card-body">
                          <p className="center card-title color">Resultados</p>
                          <p className="center card-title color">Estudiante</p>
                          <a
                            className="fuente btn-lg btn-block"
                            href={"/PageReportes/Tipo:RE"}
                            onClick={() => this.createTipo("RE")}
                            role="button"
                            >
                            VER
                          </a>
                        </div>
                      </div>
                    </div>
                           

                    <div className="col-lg-6">
                      <div className="card General">
                        <div className="card-body">
                          <p className="center card-title color">Indicadores</p>
                          <p className="center card-title color">Desempeo</p>
                          <a
                            className="fuente btn-lg btn-block"
                            href={"/PageReportes/Tipo:IN"}
                            onClick={() => this.createTipo("IN")}
                            role="button"
                          >
                            VER
                          </a>
                        </div>
                      </div>
                    </div>
                  </div>
                  </Dialog>
                ) : (
                  <Dialog
                    header="ERROR"
                    visible={this.state.visible}
                    footer={this.footer}
                    style={{ width: "50vw" }}
                    modal={true}
                    onHide={() => this.setState({ visible: false })}
                  >
                    <h1>{this.state.Mensaje}</h1>
                  </Dialog>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }
}
export default PagesReport;
