import React from "react";
import ButtonActions from "./ButtonActions";
import PageIndicadorCriterio from "./PageIndicadorCriterio";
import ModalRepor from "./ModalRepor" 
import ModalReporIndicador from "./ModalReporIndicador" 
import ModalReporResultado from "./ModalReporResultado" 
// import "./styles/Paglogin.css";
import "./styles/Pagefour.css";
import api from "../api";
import { PDFViewer } from "@react-pdf/renderer";
import { Page, Text, View, Document, StyleSheet } from "@react-pdf/renderer";
/** Este componente lista a los usuarios por cursos
 * @author: Luis Huallparimachi Saire y Joel Sumerente Ayerbe
 * @param wstoken: El parametro wstoken devuelve el token que solicitamos.
 * @param moodlewsrestformat: El parametro moodlewsrestformat permite darnos el formato en el que se mostrara                                  el contenido de la informacion.
 * @param wsfunction: El parametro wsfunction permite llamar al metodo del servicio de moodle.
 *  La funcion (core_enrol_enrolled_users) lista a los usuarios del curso.
 *                    La funcion (mod_assign_get_submissions) devuelve los envios de tareas
 * @param alumnoid: El parametro alumnoid devuelve el id del alumno que solicitamos.
 * @param assignid: El parametro assignid devuelve el id de la asignacion que solicitamos.
 * @version: 4.0 20/01/2020
 */
var assign = "assign";
var quizz = "quiz";
function traerRubrica(
  link, token, assignid, alumnoid, pCod_Curso, pCod_Asignacion, pNomRubrica
) {
  const URL =
    link +"/webservice/rest/server.php?wstoken=" + token + "&moodlewsrestformat=json&wsfunction=mod_assign_get_submission_status&userid=" +
    alumnoid + "&assignid=" + assignid;
  var item = document.createElement("p");
  fetch(URL)
    .then(res => res.json())
    .then(data => {
      if (data.lastattempt.gradingstatus === "graded") {
        item.innerHTML = data.feedback.gradefordisplay;
        let notaasig = item.querySelectorAll("tr.criterion");
        createGRubrica(
          "r" + assignid,
          pCod_Asignacion,
          pCod_Curso,
          "Rubrica (" + pNomRubrica + ")"
        );
        for (let i = 0; i < notaasig.length; i++) {
          let criterio = notaasig[i].querySelector("td.description")
            .textContent;
          createGCriterio("r" + assignid, "c" + assignid + i, i, criterio);
        }
      }
    });
}
async function createGRubrica(
  pCod_Rubrica, pCod_Asignacion,
  pCod_Curso, pNomRubrica
) {
  const cGRubrica = {
    Cod_Rubrica: pCod_Rubrica, Cod_Asignacion: pCod_Asignacion,
    Cod_Curso: pCod_Curso, NomRubrica: pNomRubrica
  };
  await api.GRubricaCrud.create(cGRubrica);
}
async function createGCriterio(
  pCod_Rubrica, pCod_Criterio,
  pNivelCriterio, pDesCriterio
) {
  const cGCriterio = {
    Cod_Rubrica: pCod_Rubrica, Cod_Criterio: pCod_Criterio,
    NivelCriterio: pNivelCriterio, DesCriterio: pDesCriterio
  };
  await api.GCriterioCrud.create(cGCriterio);
}
function traerNotas(
  link, token, assignid,
  alumnoid, pCod_Curso,
  pCod_Asignacion, pNomRubrica
) {
  const URL =
    link + "/webservice/rest/server.php?wstoken=" +
    token + "&moodlewsrestformat=json&wsfunction=mod_assign_get_submission_status&userid=" +
    alumnoid + "&assignid=" + assignid;
  var item = document.createElement("p");
  fetch(URL)
    .then(res => res.json())
    .then(data => {
      if (data.lastattempt.gradingstatus === "graded") {
        item.innerHTML = data.feedback.gradefordisplay;
        // obtiene cantidad de criterios de rubrica
        let notaasig = item.querySelectorAll("tr.criterion");
        createRubrica(
          "r" + assignid + "-" + alumnoid,
          "r" + assignid, pCod_Asignacion,
          alumnoid, pCod_Curso,
          "Rubrica (" + pNomRubrica + ")",
          "Rubrica(" + pNomRubrica + ")|" +
            data.feedback.grade.id + "|" +
            data.feedback.grade.assignment + "|" +
            data.feedback.grade.userid,
          data.feedback.grade.grade
        );
        for (let i = 0; i < notaasig.length; i++) {
          // seleciona los criterios de rubrica
          let criterio = notaasig[i].querySelector("td.description");
          // seleccion el puntaje alto de la rubrica
          let desCrt = notaasig[i].querySelector(
            "td.last > div[class=level-wrapper] > div"
          );
          // seleccion el puntaje alto de la rubrica
          let notaCrt = notaasig[i].querySelector(
            "td.last > div[class=level-wrapper] > div[class=score] > span[class=scorevalue"
          );
          // selecciona el puntaje obtenido de la rubrica
          let notaObt = notaasig[i].querySelector(
            "td[aria-checked=true] > div[class=level-wrapper] > div[class=score] > span[class=scorevalue"
          );
          // saca el puntaje objetino en el criterio
          let puntaje = notaObt.textContent / notaCrt.textContent;
          //id de asignacion > id alumno > id de criterio > descripcion de criterio > nota criterio maximo > nota criterio obtenido > porcentaje obtenido
          createCritero(
            "c" + assignid + "n" + i + "a" + alumnoid,
            "r" + assignid + "-" + alumnoid,
            i,
            criterio.textContent
          );
          createNivel(
            "c" + assignid + "n" + i + "a" + alumnoid,
            "n" + i + "as" + assignid+"st"+alumnoid,            
            desCrt.textContent,
            notaCrt.textContent,            
            notaObt.textContent,
            puntaje            
          );
        }}
    });
}
async function createRubrica(
  pCod_RubricaPersona, pCod_Rubrica, pCod_Asignacion,
  pCod_Persona, pCod_Curso, pNomRubrica, pDescripcion, pNota
) {
  const cRubrica = {
    Cod_RubricaPersona: pCod_RubricaPersona, Cod_Rubrica: pCod_Rubrica,
    Cod_Asignacion: pCod_Asignacion, Cod_Persona: pCod_Persona,
    Cod_Curso: pCod_Curso, NomRubrica: pNomRubrica, Descripcion: pDescripcion,
    Nota: pNota
  };
  await api.rubricaCrud.create(cRubrica);
}
async function createCritero(
  pCod_Criterio,
  pCod_RubricaPersona,
  pNivelCriterio,
  pDescripcionC
) {
  const cCriterio = {
    Cod_RubricaPersona: pCod_RubricaPersona,
    Cod_Criterio: pCod_Criterio,
    NivelCriterio: pNivelCriterio,
    DesCriterio: pDescripcionC
  };
  await api.CriterioCrud.create(cCriterio);
}
async function createNivel(
  pCod_Criterio, pCod_Nivel,  
  pDescripcionN, pPuntaje, 
  pPuntajeFinal, pPuntajeOb,
) {
  const cNivel = {
    Cod_Nivel: pCod_Nivel, Cod_Criterio: pCod_Criterio,
    Puntaje: pPuntaje, PuntajeObtenido : pPuntajeOb,
    PuntajeFinal: pPuntajeFinal, Descripcion: pDescripcionN
  };
  await api.nivelCrud.create(cNivel);
}
function traerAlumnos(
  link, token,
  assignid, pCod_Curso,
  pCod_Asignacion, pNomRubrica
) {
  const URL =
    link +
    "/webservice/rest/server.php?wstoken=" +
    token +
    "&moodlewsrestformat=json&wsfunction=mod_assign_get_submissions&assignmentids[0]=" +
    assignid;
  fetch(URL)
    .then(ress => ress.json())
    .then(ddata => {
      for (let y = 0; y < ddata.assignments["0"].submissions.length; y++) {
        if (ddata.assignments["0"].submissions[y].gradingstatus === "graded") {
          var alumnoid = ddata.assignments["0"].submissions[y].userid;
          traerRubrica(
            link, token, assignid,
            alumnoid,pCod_Curso, pCod_Asignacion, pNomRubrica
          );
          break;
        }
      }
      for (let y = 0; y < ddata.assignments["0"].submissions.length; y++) {
        if (ddata.assignments["0"].submissions[y].gradingstatus === "graded") {
          var alumnoids = ddata.assignments["0"].submissions[y].userid;
            traerNotas(
              link, token, assignid,
              alumnoids, pCod_Curso,
              pCod_Asignacion,pNomRubrica
            );
        }
      }
    });
}
const styles = StyleSheet.create({
  page: {
    flexDirection: "row",
    backgroundColor: "#E4E4E4",
  },
  section: {
    margin: 10,
    padding: 10,
    flexGrow: 1,
  },
});
class Pagesfour extends React.Component {
  state = {
    data: [],
    data2: {
      courses: []
    },
    tmpdata: {}
  };
  componentDidMount() {
    this.traerActividades();
    this.traerActividades2();
  }
  traerActividades = async () => {
    try {
      var URL =
        sessionStorage.getItem("url") +
        "/webservice/rest/server.php?wstoken=" +
        sessionStorage.getItem("token") +
        "&moodlewsrestformat=json&wsfunction=core_course_get_contents&courseid=" +
        sessionStorage.getItem("cursoid");
      const response = await fetch(URL);
      const data = await response.json();
      this.setState({
        data: data
      });
    } catch (error) {}
  };
  traerActividades2 = async () => {
    try {
      var URL =
        sessionStorage.getItem("url") +
        "/webservice/rest/server.php?wstoken=" +
        sessionStorage.getItem("token") +
        "&moodlewsrestformat=json&wsfunction=mod_assign_get_assignments&courseids[0]=" +
        sessionStorage.getItem("cursoid");
      const response = await fetch(URL);
      const data2 = await response.json();
      this.setState({
        data2: data2
      });
    } catch (error) {}
    this.state.data.forEach(unidad => {
      if (unidad.id == sessionStorage.getItem("unidadId")) {
        unidad.modules.forEach(modulo => {
          if (modulo.visible == 1 && modulo.modname == assign) {
            this.state.data2.courses.forEach(Curso => {
              if (Curso.id == sessionStorage.getItem("cursoid")) {
                Curso.assignments.forEach(vAssign => {
                  if (modulo.id == vAssign.cmid) {
                    this.createAsignacion(
                      vAssign.course,
                      vAssign.cmid,
                      vAssign.id,
                      vAssign.name,
                      modulo.modname,
                      0,
                      0,
                      0,
                      unidad.id,
                      1
                    );
                  }
                });
              }
            });
          }
        });
      }
    });
  };
  createAsignacion = async (
    pCod_Curso, pCod_Asignacion,
    pId_Asignacion, pNomAsignacion,
    pTipo_Asignacion, pParticipantes,
    pPresentados, pPendientes,
    pCod_Unidad, pFlag_Activo
  ) => {
    const cAsignacion = {
      Cod_Curso: pCod_Curso, Cod_Asignacion: pCod_Asignacion,
      Id_Asignacion: pId_Asignacion, NomAsignacion: pNomAsignacion,
      TipoAsignacion: pTipo_Asignacion, Participantes: pParticipantes,
      Presentados: pPresentados, Pendientes: pPendientes,
      Cod_Unidad: pCod_Unidad, Flag_Activo: pFlag_Activo
    };
    await api.asignacionCrud.create(cAsignacion);
  };
  render() {
    return (
      <div className="container">
        <br />
        <link
          rel="stylesheet"
          href="https://use.fontawesome.com/releases/v5.7.0/css/all.css"
          integrity="sha384-lZN37f5QGtY3VHgisS14W3ExzMWZxybE1SJSEsQp9S+oqd12jhcu+A56Ebc1zFSJ"
          crossorigin="anonymous"
        />
        <div className="card">
          <div className="card-body">
            {this.state.data.map(
              unidad =>
                sessionStorage.getItem("unidadId") ===
                  JSON.stringify(unidad.id) && (
                  <div>
                    <div className="titulos centrar">
                      <h1>{unidad.name}</h1>
                      <h4>ACTIVIDADES</h4>
                    </div>
                    {unidad.modules.map(
                      actividades =>
                        actividades.visible === 1 &&
                        actividades.modname === assign &&
                        this.state.data2.courses.map(verificaActi =>
                          verificaActi.assignments.map(
                            actiA =>
                              actiA.name === actividades.name && (
                                <div className="card">
                                  <div className="row">
                                    <div className="col-8">
                                      <a
                                        id={actiA.id}
                                        href={"/Pageeight/assignid:" + actiA.id}
                                        class="list-group-item list-group-item-action"
                                        onClick={() =>
                                          sessionStorage.setItem(
                                            "assignid",
                                            actiA.id
                                          )
                                        }
                                      >
                                        {actividades.name}
                                      </a>
                                      <PageIndicadorCriterio
                                        name = {actividades.name}
                                        id = {actiA.id}
                                        Cid = {actiA.cmid}
                                      />
                                      <br />
                                    </div>
                                    <div className="col-4">
                                      <ButtonActions
                                        id={actiA.id}
                                        cid={actividades.id}
                                      />
                                    </div>
                                  </div>
                                  <div id={"rubric" + actiA.id} className="row">
                                    {/* {// this.traerANotas(actiA.id)
                                    traerAlumnos(
                                      sessionStorage.getItem("url"),
                                      sessionStorage.getItem("token"),
                                      actiA.id,
                                      sessionStorage.getItem("cursoid"),
                                      actividades.id,
                                      actividades.name
                                    )} */}
                                  </div>
                                </div>
                              )
                          )
                        )
                    )}
                    {unidad.modules.map(
                      actividades =>
                        actividades.visible === 1 &&
                        actividades.modname === quizz && (
                          <div className="row">
                            <div className="col-8">
                              <a
                                id={actividades.id}
                                href="index"
                                class="list-group-item list-group-item-action"
                              >
                                {actividades.name}
                              </a>
                              <br />
                            </div>
                            <div className="col-4">
                              <ButtonActions id={actividades.id} />
                            </div>
                          </div>
                        )
                    )}
                  </div>
                )
            )}
          </div>
        </div>
        <br />
        {/* <button type="button" className="fuente boton btn-lg btn-block" >
          GENERAR REPORTE DE 1 UNIDAD
        </button>
        <ModalRepor />         */}
        <br />
      </div>
    );
  }
}
export default Pagesfour;