import React from 'react';
import { BrowserRouter, Switch, Route, } from 'react-router-dom';
import PLogin from './pages/PLogin';
import Pageone from './pages/Pageone';
import Pagetwo from './pages/Pagetwo';
import Pagethree from './pages/Pagethree';
import Pagefour from './pages/Pagefour';
import Pagefive from './pages/Pagefive';
import Pagesix from './pages/Pagesix';
import Pageseven from './pages/Pageseven';
import Pageeight from './pages/Pageeight';
import Pagenine from './pages/Pagenine';
import PageEdit from './pages/PageEdit';
import PageEditES from './pages/PageEditES';
import PageEditIN from './pages/PageEditIN';
import PageEditRE from './pages/PageEditRE';
import PageEditOB from './pages/PageEditOB';
import PageReport from './pages/PageReport';
import PageReportes from './pages/PageReportes';

import Layout from './Layout';
function App() {
  return(
    <BrowserRouter>    
    <Route exact path="/"component={PLogin}/>
      <Switch>
        <Route exact path="/Pageone/userid:userid"component={Pageone}/>
        <Route exact path="/Pagetwo/cursoid:cursoid"component={Pagetwo}/>
        <Route exact path="/Pagethree/cursoid:cursoid"component={Pagethree}/>
        <Route exact path="/Pagefour/unidadid:unidadid"component={Pagefour}/>
        <Route exact path="/Pagefive/cursoid:cursoid"component={Pagefive}/>
        <Route exact path="/Pagesix/cursoid:cursoid"component={Pagesix}/>        
        <Route exact path="/Pagenine/cursoid:cursoid"component={Pagenine}/> 
        <Route exact path="/Pageseven/assignid:assignid"component={Pageseven}/>
        <Route exact path="/Pageeight/assignid:assignid"component={Pageeight}/>
        <Route exact path="/PageEdit/userid:userid"component={PageEdit}/>
        <Route exact path="/PageEditES/userid:userid"component={PageEditES}/>
        <Route exact path="/PageEditIN/userid:userid"component={PageEditIN}/>
        <Route exact path="/PageEditRE/userid:userid"component={PageEditRE}/>
        <Route exact path="/PageEditOB/userid:userid"component={PageEditOB}/>
        <Route exact path="/PageReport/userid:userid"component={PageReport}/>
        <Route exact path="/PageReportes/Tipo:Tipo"component={PageReportes}/>
      </Switch>
      <Layout> </Layout>
  </BrowserRouter>
  );
}
export default App;