import React from "react";
import "./styles/Edicion.css";
import { Button, ButtonGroup, Modal, ModalHeader, ModalBody, ModalFooter, InputGroup, InputGroupAddon, Input } from "reactstrap";
import api from "../api";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { InputText } from "primereact/inputtext";
import { Growl } from "primereact/growl";
import "primereact/resources/themes/nova-light/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
class PageEditES extends React.Component {
  constructor() {
    super();
    this.state = {
      dtEscuela: [],
      modalIsOpen: false,
      escu2: null
    };
    this.dtUpdateEsc = {};
    this.Cod_EscuelaEditor = this.Cod_EscuelaEditor.bind(this);
    this.SedeEditor = this.SedeEditor.bind(this);
    this.DescripcionEditor = this.DescripcionEditor.bind(this);
    this.requiredValidator = this.requiredValidator.bind(this);
    this.editorForRowEditing = this.editorForRowEditing.bind(this);
    this.onRowEditorValidator = this.onRowEditorValidator.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditInit = this.onRowEditInit.bind(this);
    this.onRowEditSave = this.onRowEditSave.bind(this);
    this.onRowEditCancel = this.onRowEditCancel.bind(this);
  }
  ToggleModal() {
    this.setState({
      modalIsOpen: !this.state.modalIsOpen
    });
  }
handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  createEscuela = async e => {
    const cEscuela = {
      Cod_Escuela: this.state.Cod_Escuela,
      Sede: this.state.Sede,
      Descripcion: this.state.Descripcion
    };
    console.log(cEscuela);
    await api.escuelaCrud.create(cEscuela);
    this.refreshPage();
  };
  updateEscuela = async (pCod_Escuela, pSede, pDescripcion) => {
    const cEscuela = {
      Cod_Escuela: pCod_Escuela,
      Sede: pSede,
      Descripcion: pDescripcion
    };
    await api.escuelaCrud.create(cEscuela);
  };
  deleteEscuela = async (dCod_Escuela) => {
    const pCod_Escuela = {
      Cod_Escuela: dCod_Escuela
    };
    await api.escuelaCrud.remove(pCod_Escuela);
    this.refreshPage();
  };
  componentDidMount() {
    this.traerEscuela();
  }
  traerEscuela = async () => {
    try {
      const dtEscuela = await api.escuelaCrud.list();
      this.setState({
        dtEscuela: dtEscuela.Escuelas
      });
    } catch (error) {}
  };
  onRowEditorValidator(rowData) {
    let value = rowData["Descripcion"];
    return value.length > 0;
  }
  Cod_EscuelaEditor(props) {
    return this.inputTextEditor(props, "Cod_Escuela");
  }
  DescripcionEditor(props) {
    return this.inputTextEditor(props, "Descripcion");
  }
  SedeEditor(props) {
    return this.inputTextEditor(props, "Sede");
  }
  requiredValidator(props) {
    let value = props.rowData[props.field];
    return value && value.length > 0;
  }
  onEditorValueChange(props, value) {
    let updatedEsc = [...props.value];
    updatedEsc[props.rowIndex][props.field] = value;
    this.setState({ escu2: updatedEsc });
  }
  onRowEditInit(event) {
    this.dtUpdateEsc[event.data.Cod_Escuela] = { ...event.data };
    this.escu2 = { ...event.data };
  }
  onRowEditSave(event) {
    if (this.onRowEditorValidator(event.data)) {
      this.updateEscuela(
        event.data.Cod_Objetivo,
        event.data.Sede,
        event.data.Descripcion
      );
      this.growl.show({
        severity: "Actualizado",
        summary: "Exitoso!!",
        detail: "ES is updated"
      });
    } else {
      this.growl.show({
        severity: "error",
        summary: "Error",
        detail: "ES is required"
      });
    }
  }
  onRowEditCancel(event) {
    let rEscuela = [...this.state.dtEscuela];
    rEscuela[event.index] = this.dtUpdateEsc[event.data.Cod_Escuela];
    delete this.dtUpdateEsc[event.data.Cod_Escuela];
    this.setState({
      dtEscuela: rEscuela
    });
  }
  onEditorValueChangeForRowEditing = async (props, value) => {
    let updatedEsc = [...props.value];
    updatedEsc[props.rowIndex][props.field] = value;
    this.setState({ escu2: updatedEsc });
  };
  editorForRowEditing(props, field) {
    return (
      <InputText
        type="text"
        value={props.rowData[field]}
        onChange={e =>
          this.onEditorValueChangeForRowEditing(props, e.target.value)
        }
      />
    );
  }
  refreshPage() {
    window.location.reload(true);
  }
  displaySelection(data) {
    if (!data || data.length === 0) {
      return <div></div>;
    } else {
      if (data instanceof Array)
        return (
          <ul style={{ textAlign: "left", margin: 0 }}>
            {data.map((slEscuela, i) => (
              <button key={slEscuela.Cod_Escuela}>
                {slEscuela.Cod_Escuela + " - " + slEscuela.Cod_Escuela}
              </button>
            ))}
          </ul>
        );
      else
        return (
          <button
            onClick={e => this.deleteEscuela(data.Cod_Escuela)}
            style={{ textAlign: "left" }}
          >
            Eliminar: {data.Cod_Escuela}
          </button>
        );
    }
  }
  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="centro">
            <h1>Escuela</h1>
          </div>
        </div>
        <div className="row centro2 abajo">
          <h2>Lista de Escuelas</h2>
          <div className="col espacioss">
            <ButtonGroup>
              <div className="col">
                <Button
                  outline
                  color="primary mr-2"
                  size="lg"
                  onClick={this.ToggleModal.bind(this)}
                  label="Nuevo"
                >
                  <i class="fas fa-plus-circle"></i>
                </Button>
                <Modal isOpen={this.state.modalIsOpen}>
                  <ModalHeader toggle={this.ToggleModal.bind(this)}>
                    Nueva Escuela Profesional
                  </ModalHeader>
                  <ModalBody>
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Codigo Escuela
                      </InputGroupAddon>
                      <Input
                        placeholder="Codigo Escuela Profesional"
                        name="Cod_Escuela"
                        value={this.state.Cod_Escuela}
                        onChange={this.handleChange}
                      />
                    </InputGroup>
                    <br />
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Sede
                      </InputGroupAddon>
                      <Input
                        placeholder="Sede de la Escuela"
                        name="Sede"
                        value={this.state.sede}
                        onChange={this.handleChange}
                      />
                    </InputGroup>
                    <br />
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        Descripcion
                      </InputGroupAddon>
                      <Input
                        placeholder="Descripcion de la Escuela"
                        name="Descripcion"
                        value={this.state.Descripcion}
                        onChange={this.handleChange}
                      />
                    </InputGroup>
                  </ModalBody>
                  <ModalFooter>
                    <Button
                      color="primary"
                      onBlur={this.createEscuela}
                      onClick={this.ToggleModal.bind(this)}
                    >
                      GUARDAR
                    </Button>
                    <Button
                      color="danger"
                      onClick={this.ToggleModal.bind(this)}
                    >
                      CANCELAR
                    </Button>
                  </ModalFooter>
                </Modal>
              </div>
            </ButtonGroup>
          </div>
        </div>
        <div className="">
          <Growl ref={el => (this.growl = el)} />
          <DataTable
            value={this.state.dtEscuela}
            selectionMode="single"
            selection={this.state.selecEscuela}
            header={this.displaySelection(this.state.selecEscuela)}
            onSelectionChange={e => this.setState({ selecEscuela: e.value })}
            responsive={true}
            editMode="row"
            metaKeySelection={false}
            rowEditorValidator={this.onRowEditorValidator}
            onRowEditInit={this.onRowEditInit}
            onRowEditSave={this.onRowEditSave}
            onRowEditCancel={this.onRowEditCancel}
          >
            <Column
              field="Cod_Escuela"
              header="Escuela"
              style={{ textAlign: "center", width: "28%" }}
            />
            <Column
              field="Sede"
              header="Sede"
              style={{ textAlign: "center", width: "15%" }}
              editor={props => this.editorForRowEditing(props, "Sede")}
            />
            <Column
              field="Descripcion"
              header="Descripcion"
              style={{ textAlign: "center", width: "55%" }}
              editor={props => this.editorForRowEditing(props, "Descripcion")}
            />
            <Column
              rowEditor={true}
              style={{ width: "7%", textAlign: "center" }}
            ></Column>
          </DataTable>
        </div>
      </div>
    );
  }
}
export default PageEditES;