import React from 'react';
import './styles/ButtonActions.css';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import api from "../api";
import {Checkbox} from 'primereact/checkbox';
// import Iframe from 'react-iframe'

class ButtonActions extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: {
        gradingsummary: {
          participantcount: {},
          submissionssubmittedcount: {},
        },
        warnings: [],
      },
      checked: false,
      valor: false,
    }
    this.onCheckChange = this.onCheckChange.bind(this);
  }
  componentDidMount() {
    this.traerUsuario();
    const cEstado = {
      Cod_Curso: sessionStorage.getItem("cursoid"),
      Cod_Asignacion : (this.props.cid),
      Id_Asignacion : (this.props.id)
    };
    const cValidar = {
      Cod_Curso: sessionStorage.getItem("cursoid"),
      Cod_Rubrica : "R"+(this.props.id)
    };
    this.traerEstado(cEstado);
    this.traerValidar(cValidar);
  }

  AEstado = async (pFlag_Estado) =>{
    const cAsignacion = {
      Cod_Curso : sessionStorage.getItem("cursoid"),
      Cod_Asignacion : (this.props.cid),
      Flag_Estado: pFlag_Estado,
      Id_Asignacion : (this.props.id)
    };    
    await api.asignacionCrud.AEstado(cAsignacion);
    //  console.log(cAsignacion);
  }

  onCheckChange(e){
      this.setState({
        checked: e.checked,
      });
      this.AEstado(e.checked);
  }

  traerEstado = async cEstado => {
    try {
      const Checked = await api.asignacionCrud.TEstado(
        cEstado
      );
      this.setState({
        checked: Checked.RubricaAsignacion
      });
    } catch (error) {}
  };

  traerValidar = async cValiEstado => {
    try {
      const validacion = await api.asignacionCrud.TEstadoValidacion(
        cValiEstado
      );
      this.setState({
        valor: validacion.RubricaAsignacion[0].MERROR
      });
      // console.log(this.props.id+validacion.RubricaAsignacion[0].MERROR);
    } catch (error) {}
    
  };

  traerUsuario = async () => {
    try {
      var URL = (sessionStorage.getItem('url')) + '/webservice/rest/server.php?wstoken=' + (sessionStorage.getItem('token')) +
        '&moodlewsrestformat=json&wsfunction=mod_assign_get_submission_status&assignid=' + (this.props.id)
      const response = await fetch(URL);
      const data = await response.json();
      this.setState({
        data: {
          gradingsummary: {
            participantcount: data.gradingsummary.participantcount,
            submissiondraftscount: data.gradingsummary.submissiondraftscount,
            submissionssubmittedcount: data.gradingsummary.submissionssubmittedcount,
            submissionsneedgradingcount: data.gradingsummary.submissionsneedgradingcount
          },
          warnings: data.warnings
        },
      });
    }
    catch (error) {
    }
    this.updateAsignacion(this.props.cid, this.state.data.gradingsummary.participantcount, this.state.data.gradingsummary.submissionssubmittedcount, this.state.data.gradingsummary.submissionsneedgradingcount);
  };
  updateAsignacion = async(pCod_Asignacion, pParticipantes, pPresentados, pPendientes) =>{
    const cAsignacion = {
      Cod_Asignacion : pCod_Asignacion,
      Participantes : pParticipantes,
      Presentados : pPresentados,
      Pendientes : pPendientes
    };    
    await api.asignacionCrud.upAsignacion(cAsignacion);
  }
render(){
return (
<div>
  <div>
  </div>
  <div>
    <ul className="nav">
    <li className="nav-item">
    <a className="nav-link entrespacios" data-toggle="tooltip" data-placement="top" title={this.props.id}>
        <Checkbox onChange={this.onCheckChange} checked={this.state.checked}  ></Checkbox>
        {/* <input type="checkbox" onChange={this.onCheckChange} checked={this.state.checked} readOnly={this.state.valor}></input> */}
        </a>
      </li>     
      
      <li className="nav-item">
        <a className="nav-link entrespacios"  data-toggle="tooltip" data-placement="top" title={'Calificar '+ (this.state.data.gradingsummary.submissionsneedgradingcount)}>
                <i class="fas fa-file"></i><span className="sr-only"></span></a>
            </li>
            <li className="nav-item">
              <a className="nav-link entrespacios" data-toggle="tooltip" data-placement="top"
               title={' Falta Entregar '+ (this.state.data.gradingsummary.submissionssubmittedcount)}>
                <i class="fas fa-archive"></i><span className="sr-only"></span></a>
            </li>
            <li className="nav-item">
              <a className="nav-link entrespacios"  data-toggle="tooltip" data-placement="top" title={this.state.data.gradingsummary.participantcount}>
                <i class="fas fa-user" onClick={this.toggle}></i><span className="sr-only"></span></a>
                
            </li>
        </ul>
       </div>
      </div>
    );
  }
}
export default ButtonActions;